import React from "react";
import RadioCard from "./RadioCard";
import { HStack, useRadioGroup } from "@chakra-ui/react";

function DurationSelector({ setLockingDuration, staking_options }) {
  const handleChange = (value) => {
    setLockingDuration(value);
  };

  const { getRootProps, getRadioProps } = useRadioGroup({
    name: "durationSelector",
    defaultValue: "30",
    onChange: handleChange,
  });

  const group = getRootProps();

  return (
    <HStack {...group} display="grid" gridTemplateColumns="repeat(4,1fr)">
      {staking_options.map((el) => {
        // const radio = getRadioProps({ value });
        return (
          <RadioCard
            key={el.time}
            {...getRadioProps({ value: el.timeInSeconds })}
          >
            {el}
          </RadioCard>
        );
      })}
    </HStack>
  );
}

export default DurationSelector;
