import { Button } from "@chakra-ui/react";
import { useCallback } from "react";
import iconSwap from "../../assets/icon_swap.svg";
import iconSwapOneWay from "../../assets/icon_swap_one_way.svg";

function SwapToFromToken({
  toCoin,
  setToCoin,
  toCoinAmount,
  setToCoinAmount,
  fromCoin,
  setFromCoin,
  fromCoinAmount,
  setFromCoinAmount,
  setNativeValue,
}) {
  const swapAmount = useCallback(() => {
    const tempFromTokenAmount = fromCoinAmount;
    const tempToTokenAmount = toCoinAmount;

    // console.log(`--- Old Amount Values ---`);
    // console.log(`fromTokenAmount - ${tempFromTokenAmount}`);
    // console.log(`toTokenAmount - ${tempToTokenAmount}`);

    setFromCoinAmount(tempToTokenAmount);
    setToCoinAmount(tempFromTokenAmount);
    // console.log(`--- New Values ---`);
    // console.log(`fromTokenAmount - ${fromCoinAmount}`);
    // console.log(`toTokenAmount - ${toCoinAmount}`);

    let fromCoinAmountEl = document.getElementById("swap__fromCoinAmount");
    let toCoinAmountEl = document.getElementById("swap__toCoinAmount");
    setNativeValue(fromCoinAmountEl, toCoinAmount);
    setNativeValue(toCoinAmountEl, fromCoinAmount);
    toCoinAmountEl.dispatchEvent(new Event("input", { bubbles: true }));
    fromCoinAmountEl.dispatchEvent(new Event("input", { bubbles: true }));
  }, [
    fromCoinAmount,
    setFromCoinAmount,
    setNativeValue,
    setToCoinAmount,
    toCoinAmount,
  ]);

  const swapFromAndToToken = useCallback(() => {
    // console.log(`Swaping From&To tokens`);
    // Store old values of coins & their amount
    const tempFromToken = fromCoin;
    const tempToToken = toCoin;
    // console.log(`--- Old Values ---`);
    // console.log(`toToken - ${tempToToken}`);
    // console.log(`fromToken - ${tempFromToken}`);

    // Swap old values of coin amount and token
    setFromCoin(tempToToken);
    setToCoin(tempFromToken);
    swapAmount();
  }, [fromCoin, setFromCoin, setToCoin, toCoin, swapAmount]);

  return (
    <Button
      width="3rem"
      height="3rem"
      borderRadius="100%"
      margin="0.5rem auto"
      marginTop="2rem"
      padding="0px"
      // style={{
      //   background: `linear-gradient(90deg, #181652cc, #172C66cc) padding-box, linear-gradient(45deg, rgb(137, 210, 243), rgb(166, 124, 255)) border-box `,
      //   border: "3px solid transparent",
      // }}
      onClick={swapFromAndToToken}
    >
      <img
        src={iconSwapOneWay}
        alt=""
        style={{
          width: "2.6rem",
          height: "2.6rem",
          borderRadius: "100%",
          // background: "red",
          padding: "8px",
          background: `linear-gradient(90deg, #181652cc, #172C66cc) padding-box, linear-gradient(45deg, rgb(137, 210, 243), rgb(166, 124, 255)) border-box `,
          border: "3px solid transparent",
        }}
        onMouseOver={(e) => {
          e.currentTarget.src = iconSwap;
          e.currentTarget.style.padding = "7px";
        }}
        onMouseOut={(e) => {
          e.currentTarget.src = iconSwapOneWay;
          e.currentTarget.style.padding = "8px";
        }}
      />
    </Button>
  );
}

export default SwapToFromToken;
