import Staking from "./Staking";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import SwapAndLiquidity from "./swap/SwapAndLiquidity";
import Sidebar from "./Sidebar";

function Main({
  login,
  flexAllowance,
  setFlexAllowance,
  lockedAllowance,
  setLockedAllowance,
  totalIbatStaked,
  setTotalIbatStaked,
  getTotalIbatStaked,
  showGenericErrorMessage,
  firebaseApp,
  tokenPriceUSD,
  firestoreUSDPriceLoading,
}) {
  return (
    <main style={{ paddingBottom: "3rem" }}>
      <Router>
        <Sidebar />
        <Switch>
          <Route path="/" exact>
            <Staking
              login={login}
              flexAllowance={flexAllowance}
              setFlexAllowance={setFlexAllowance}
              lockedAllowance={lockedAllowance}
              setLockedAllowance={setLockedAllowance}
              totalIbatStaked={totalIbatStaked}
              setTotalIbatStaked={setTotalIbatStaked}
              getTotalIbatStaked={getTotalIbatStaked}
              showGenericErrorMessage={showGenericErrorMessage}
              firebaseApp={firebaseApp}
              tokenPriceUSD={tokenPriceUSD}
              firestoreUSDPriceLoading={firestoreUSDPriceLoading}
            ></Staking>
          </Route>
          <Route path="/swap" exact>
            <SwapAndLiquidity login={login} />
          </Route>
        </Switch>
      </Router>
    </main>
  );
}

export default Main;
