import { Flex, Icon, Text, ListItem } from "@chakra-ui/react";
import { useState } from "react";
import { NavLink } from "react-router-dom";

function NavIcon({ navText, iconPath, linkTo, viewBox }) {
  const [isLinkActive, setIsLinkActive] = useState(false);
  return (
    <ListItem
      width={"max-content"}
      _hover={{ color: "#4113be80", backgroundColor: "#4113be80" }}
      _active={{ color: "#4113be80", backgroundColor: "#4113be80" }}
      borderRadius="lg"
    >
      <NavLink
        to={linkTo ? linkTo : "/"}
        isActive={(match) => {
          if (!match) {
            // Using Timeout @ ln-23 & ln-28 coz of
            // error - cannot update a component (`NavIcon`) while rendering a different component
            // Link : https://stackoverflow.com/questions/62336340/cannot-update-a-component-while-rendering-a-different-component-warning

            // setIsLinkActive(false);
            setTimeout(() => setIsLinkActive(false), 0);
            return false;
          }

          if (linkTo && match && linkTo === match.url)
            setTimeout(() => setIsLinkActive(true), 0);
          else setTimeout(() => setIsLinkActive(false), 0);

          // if (linkTo === match.url) setIsLinkActive(true);
          // else setIsLinkActive(false);
        }}
      >
        <Flex
          alignItems={"center"}
          direction="column"
          padding=".25rem"
          width={"18vw"}
          maxWidth="70px"
          background={isLinkActive ? "#140C21" : ""}
          borderRadius="lg"
        >
          <Icon
            viewBox={viewBox ? viewBox : "0 0 22 22"}
            color={isLinkActive ? "#DDCDEA" : "#050a5c"}
            _hover={{ color: isLinkActive ? "#DDCDEA" : "#050a5c" }}
            _active={{ backgroundColor: isLinkActive ? "#DDCDEA" : "#050a5c" }}
            width="1.75rem"
            height="1.75rem"
            pointerEvents={"none"}
          >
            <path fill="currentColor" d={iconPath} />
          </Icon>
          <Text
            fontSize={"x-small"}
            textTransform="uppercase"
            color={isLinkActive ? "#DDCDEA" : "#050a5c"}
            fontWeight="semibold"
          >
            {navText}
          </Text>
        </Flex>
      </NavLink>
    </ListItem>
  );
}

export default NavIcon;
