import React, { useCallback, useState, useEffect } from "react";
import {
  Box,
  Flex,
  Skeleton,
  Stat,
  StatLabel,
  StatNumber,
  Tabs,
  TabList,
  TabPanels,
  Tab,
  TabPanel,
  Text,
  useToast,
  useMediaQuery,
} from "@chakra-ui/react";
import FlexibleStaking from "./FlexibleStaking";
import LockedStaking from "./LockedStaking";
import ConnectWalletDrawer from "./ConnectWalletDrawer";
import {
  useMoralis,
  useWeb3ExecuteFunction,
  useERC20Balances,
} from "react-moralis";
import STAKING_BG from "../assets/staking/battle_staking.png";

import {
  CONTRACT_ADDRESS_LOCKED_STAKING,
  CONTRACT_ADDRESS_FLEXIBLE_STAKING,
  CONTRACT_ADDRESS_IBAT_TOKEN,
  IBAT_DECIMALS,
  isTestnet,
} from "../assets/StakingABI";

// FIREBASE - START - for totalTVL && tokenPriceUSD
import { getFirestore, doc } from "firebase/firestore";
import { useDocument } from "react-firebase-hooks/firestore";
import ExternalLink from "./ExternalLink";
// FIREBASE - END

function Staking({
  login,
  flexAllowance,
  setFlexAllowance,
  lockedAllowance,
  setLockedAllowance,
  totalIbatStaked,
  setTotalIbatStaked,
  getTotalIbatStaked,
  showGenericErrorMessage,
  firebaseApp,
  tokenPriceUSD,
  firestoreUSDPriceLoading,
}) {
  const [isLargerThan600] = useMediaQuery("(min-width: 600px)");

  const { isAuthenticated, Moralis } = useMoralis();

  const { fetchERC20Balances } = useERC20Balances();
  const convertToDecimals = useCallback(
    (amount) => {
      let v = amount;
      v = 0;
      try {
        amount += "";
        v = Moralis.Units.FromWei(amount, IBAT_DECIMALS);
        // v = Moralis.Units.FromWei(
        //   Moralis.web3Library.BigNumber.from(amount),
        //   IBAT_DECIMALS
        // ); // this works basically same as Without entering the argument `amount` without the Big.Number so using it wihout BigNumber for the time being
      } catch (error) {
        v = Number(amount) / Math.pow(10, IBAT_DECIMALS);
        console.error(error);
        console.error(`dev-convertToDecimals() - main.js`);
      }
      return v;
    },
    [Moralis.Units]
  );

  const toast = useToast();
  const { fetch, isFetchingEnableStaking } = useWeb3ExecuteFunction();
  const enableStaking = async (contractAddressToAllow) => {
    // make sure that amount input is more than minimum
    let ABI_approve = [
      {
        inputs: [
          { internalType: "address", name: "spender", type: "address" },
          { internalType: "uint256", name: "amount", type: "uint256" },
        ],
        name: "approve",
        outputs: [{ internalType: "bool", name: "", type: "bool" }],
        stateMutability: "nonpayable",
        type: "function",
      },
    ];
    let optionsApprove = {
      abi: ABI_approve,
      contractAddress: CONTRACT_ADDRESS_IBAT_TOKEN,
      functionName: "approve",
      params: {
        spender: contractAddressToAllow,
        amount: "10000000000000000000000000000",
      },
    };
    await fetch({
      params: optionsApprove,
      onSuccess: (tx) => {
        console.log(tx);
        console.log(`dev-✅-Ran function deposit successfully`);
        toast({
          containerStyle: {
            maxWidth: "92vw",
          },
          description: `Enable Staking Tx in progress...`,
          status: "info",
          duration: null,
          isClosable: true,
          position: "bottom-left",
        });

        tx.wait().then((dataa) => {
          console.log(dataa);
          let contractType = "Staking";
          if (contractAddressToAllow === CONTRACT_ADDRESS_FLEXIBLE_STAKING) {
            contractType = "Flexible Staking";
            window.localStorage.setItem("flexAllowance", "true");
            setFlexAllowance("true");
          }
          if (contractAddressToAllow === CONTRACT_ADDRESS_LOCKED_STAKING) {
            contractType = "Locked Staking";
            window.localStorage.setItem("lockedAllowance", "true");
            setLockedAllowance("true");
          }

          toast.closeAll({ positions: ["bottom-left"] });
          toast({
            containerStyle: {
              maxWidth: "92vw",
            },
            title: `${contractType} Enabled`,
            description: `For best Experience, disconnect & connect again.`,
            status: "success",
            duration: "10000",
            isClosable: true,
            position: "bottom-left",
          });
        });
      },
      onError: (err) => {
        console.error(err);
        if (err && err.data) showGenericErrorMessage(err.data.message);
        else if (err && err.message) showGenericErrorMessage(err.message);
        console.error(`dev-failed to run deposit function`);
      },
    });
  };

  const [totalIbatBalance, settotalIbatBalance] = useState(0);
  const getIBATBalance = useCallback(async () => {
    if (!isAuthenticated) {
      console.log(
        `dev-failed to fetch token balances - isAuthenticated : ${isAuthenticated}`
      );
      return;
    }
    const tokenBalances = await fetchERC20Balances({
      params: { chain: Moralis.chainId },
    });
    tokenBalances &&
      tokenBalances.forEach((token) => {
        if (
          CONTRACT_ADDRESS_IBAT_TOKEN.toLowerCase() ===
          token.token_address.toLowerCase()
        ) {
          let bal = token.balance;
          bal = Moralis.Units.FromWei(bal, IBAT_DECIMALS);
          settotalIbatBalance(bal);
        }
      });
  }, [fetchERC20Balances, isAuthenticated, Moralis.chainId, Moralis.Units]);

  function setNativeValue(element, value) {
    const valueSetter = Object.getOwnPropertyDescriptor(element, "value").set;
    const prototype = Object.getPrototypeOf(element);
    const prototypeValueSetter = Object.getOwnPropertyDescriptor(
      prototype,
      "value"
    ).set;

    if (valueSetter && valueSetter !== prototypeValueSetter) {
      prototypeValueSetter.call(element, value);
    } else {
      valueSetter.call(element, value);
    }
  }

  /****************
   * START
   * Total TVL (locked + flexible)
   *******************/
  // get Flexible TVL
  const db = getFirestore(firebaseApp);
  const STAKING_DB_COLLECTION_NAME = "stakingmainnet";
  const STAKING_FLEXIBLE = "flexible";
  const STAKING_LOCKED = "locked";
  const lockedTVLRef = doc(db, STAKING_DB_COLLECTION_NAME, STAKING_LOCKED);
  const flexibleTVLRef = doc(db, STAKING_DB_COLLECTION_NAME, STAKING_FLEXIBLE);

  const [flexibleTVLValue, flexibleTVLLoading] = useDocument(flexibleTVLRef, {
    snapshotListenOptions: { includeMetadataChanges: true },
  });
  const [lockedTVLValue, lockedTVLLoading] = useDocument(lockedTVLRef, {
    snapshotListenOptions: { includeMetadataChanges: true },
  });

  const [totalTVL, setTotalTVL] = useState("");
  const updateTotalTVL = useCallback(async () => {
    let flexibleTVL = 0;
    !flexibleTVLLoading &&
      flexibleTVLValue &&
      flexibleTVLValue.exists() &&
      (flexibleTVL = Number(flexibleTVLValue.data().tvl));
    // get Locked TVL
    let lockedTVL = 0;
    try {
      !lockedTVLLoading &&
        lockedTVLValue &&
        lockedTVLValue.exists() &&
        (lockedTVL = Number(lockedTVLValue.data().tvl));
    } catch (error) {
      console.error(error);
      console.error(`dev-some-error-when-net-off`);
    }

    // sum them
    let totalTVLinIBAT = flexibleTVL + lockedTVL;
    // totalTVLinIBAT = totalTVLinIBAT.toLocaleString("en-US");
    let totalTVL = totalTVLinIBAT * tokenPriceUSD;
    totalTVL = Number(totalTVL.toFixed(2));
    totalTVL = totalTVL.toLocaleString("en-US");
    // & update in Firebase
    try {
      if (isTestnet) {
        flexibleTVLValue &&
          lockedTVLValue &&
          console.log(
            `flexibleTVLLoading - ${flexibleTVLLoading}\nflexibleTVLValue - ${
              flexibleTVLValue.data().tvl
            }\nlockedTVLLoading - ${lockedTVLLoading}\nlockedTVLValue - ${
              lockedTVLValue.data().tvl
            }\ntokenPriceUSD - ${tokenPriceUSD}\n`
          );
        console.log(`🌊🌊🌊 flexibleTVL - ${flexibleTVL} 🌊🌊🌊`);
        console.log(`🔐🔐🔐 lockedTVL - ${lockedTVL} 🔐🔐🔐`);
        console.log(`🌈🌈🌈 totalTVLtemp - ${totalTVL} 🌈🌈🌈`);
        console.log(`--------------------`);
      }
    } catch (error) {
      console.error(error);
      console.error(`dev-some-error-when-net-off`);
    }
    setTotalTVL(totalTVL);
  }, [
    flexibleTVLLoading,
    flexibleTVLValue,
    lockedTVLLoading,
    lockedTVLValue,
    tokenPriceUSD,
  ]);

  useEffect(updateTotalTVL, [updateTotalTVL]);

  /****************
   * Total TVL (locked + flexible)
   * END
   *******************/
  return (
    <Box
      backgroundImage={STAKING_BG}
      backgroundPosition={"85% 80%"}
      minHeight="80vh"
      bgAttachment={"fixed"}
      backgroundRepeat="no-repeat"
      bgSize={isLargerThan600 ? "auto" : "350px"}
    >
      <Stat
        borderRadius="2xl"
        background="linear-gradient(270deg, #066100cc 0%, #005249cc 50%) padding-box,linear-gradient(45deg, #89d2f3, #a67cff) border-box!important"
        border="3px solid transparent !important"
        style={{
          padding: "1rem 2rem",
          width: "max-content",
          minWidth: "265px",
          margin: "1rem auto ",
          boxShadow: "0px 0px 50px 0px #743680",
          minHeight: "5.7rem",
          paddingBottom: ".5rem",
        }}
      >
        {/* <Text fontSize=".8rem">Total Value Locked</Text>
        <StatLabel fontSize="2xl" textTransform="uppercase" fontWeight="black">
          TVL
        </StatLabel> */}
        <StatLabel fontSize="sm" textTransform="uppercase" fontWeight="black">
          Total Value Locked
        </StatLabel>
        <StatNumber display="flex" width="max-content" marginX="auto">
          {firestoreUSDPriceLoading ||
          flexibleTVLLoading ||
          lockedTVLLoading ? (
            <Skeleton
              height="2rem"
              width="12rem"
              margin="auto"
              display="inline-block"
              marginBottom=".2rem"
            />
          ) : (
            <Text>${totalTVL}</Text>
          )}
        </StatNumber>
        <Flex width="max-content" marginX="auto">
          {firestoreUSDPriceLoading ||
          flexibleTVLLoading ||
          lockedTVLLoading ? (
            <Skeleton
              height="1.3rem"
              width="8rem"
              margin="auto"
              alignItems="center"
            />
          ) : (
            <Text fontSize="x-small">{`$ ${tokenPriceUSD} = 1 IBAT`}</Text>
          )}
        </Flex>
      </Stat>
      <Tabs variant="soft-rounded" colorScheme="green" defaultIndex={1}>
        <TabList
          className="gradient-2"
          style={{
            width: "max-content",
            margin: "auto",
            borderRadius: "50px",
            padding: ".2rem",
            minWidth: "250px",
          }}
        >
          <Tab
            _selected={{
              background:
                "linear-gradient(98.76deg, #38C3CF 0%, #8767D3 100%);",
              border: "none",
              color: "#fff",
            }}
            textTransform="uppercase !important"
            color="#ccc"
            width="50%"
            minWidth="125px"
          >
            Flexible
          </Tab>
          <Tab
            _selected={{
              background:
                "linear-gradient(98.76deg, #38C3CF 0%, #8767D3 100%);",
              border: "none",
              color: "#fff",
            }}
            textTransform="uppercase !important"
            color="#ccc"
            width="50%"
            minWidth="125px"
          >
            Locked
          </Tab>
        </TabList>
        <TabPanels>
          <TabPanel>
            <FlexibleStaking
              login={login}
              flexAllowance={flexAllowance}
              totalIbatStaked={totalIbatStaked}
              setTotalIbatStaked={setTotalIbatStaked}
              getTotalIbatStaked={getTotalIbatStaked}
              enableStaking={enableStaking}
              isFetchingEnableStaking={isFetchingEnableStaking}
              firebaseApp={firebaseApp}
              convertToDecimals={convertToDecimals}
              getIBATBalance={getIBATBalance}
              totalIbatBalance={totalIbatBalance}
              setNativeValue={setNativeValue}
              showGenericErrorMessage={showGenericErrorMessage}
            ></FlexibleStaking>
          </TabPanel>
          <TabPanel>
            <LockedStaking
              lockedAllowance={lockedAllowance}
              enableStaking={enableStaking}
              isFetchingEnableStaking={isFetchingEnableStaking}
              firebaseApp={firebaseApp}
              convertToDecimals={convertToDecimals}
              getIBATBalance={getIBATBalance}
              totalIbatBalance={totalIbatBalance}
              setNativeValue={setNativeValue}
              showGenericErrorMessage={showGenericErrorMessage}
            />
          </TabPanel>
        </TabPanels>
      </Tabs>
      {!isAuthenticated && (
        <>
          <section
            className="staking__container staking__selector gradient-2"
            style={{
              padding: "2rem max(9vw, 2rem)",
              width: "max-content",
              margin: "auto",
            }}
          >
            <Text>Connect your wallet to stake IBAT tokens!</Text>
            <ConnectWalletDrawer
              login={login}
              addClass={"btn__connect_wallet--staking"}
            />
          </section>
        </>
      )}
      <ExternalLink
        linkText="How to Stake?"
        link="https://battleinfinity.io/staking"
      />
    </Box>
  );
}

export default Staking;
