function LockedStaking({
  lockedAllowance,
  enableStaking,
  isFetchingEnableStaking,
  firebaseApp,
  convertToDecimals,
  getIBATBalance,
  totalIbatBalance,
  setNativeValue,
  showGenericErrorMessage,
}) {
  const { Moralis, isAuthenticated, isAuthenticating, user, isWeb3Enabled } =
    useMoralis();
  const toast = useToast();
  const { fetch, isFetching } = useWeb3ExecuteFunction();

  const [userStakedAmountinLocked, setUserStakedAmountinLocked] = useState("0");
  const [totalStakingTxs, setTotalStakingTxs] = useState(0);
  // const [totalIbatStakedinLocked, setTotalIbatStakedinLocked] = useState("0");
  const [userTransactions, setUserTransactions] = useState([]);
  const [userTransactionsLoaded, setUserTransactionsLoaded] = useState(false);
  const [userTotalRewards, setUserTotalRewards] = useState("0"); //overall: total = locked + unlocked rewards // locked rewards not calclated yet
  const [userTotalUnlockedRewards, setUserTotalUnlockedRewards] = useState("0");
  const [userTotalLockedRewards, setUserTotalLockedRewards] = useState("0");

  /**
   * START
   * Firebase stuff
   */
  const db = getFirestore(firebaseApp);
  const DB_COLLECTION_NAME = firebaseCol;
  const DB_DOC_NAME = "locked";
  const lockedTVLRef = doc(db, DB_COLLECTION_NAME, DB_DOC_NAME);

  const [firestoreValue, firestoreLoading] = useDocument(lockedTVLRef, {
    snapshotListenOptions: { includeMetadataChanges: true },
  });

  /**
   * Firebase stuff
   * END
   */

  const getRewards = useCallback(
    async (txNo) => {
      let rewardForThisTxNo = 0;
      if (isAuthenticated && user && isWeb3Enabled) {
        let userAddress = user
          ? user.get("ethAddress")
          : window.localStorage.getItem("userAddress");
        const fnName = "rewards";
        let optionsLocked = {
          abi: ABI_LOCKED_STAKING,
          contractAddress: CONTRACT_ADDRESS_LOCKED_STAKING,
        };
        try {
          await fetch({
            params: {
              ...optionsLocked,
              functionName: fnName,
              params: {
                _user: userAddress,
                _txNo: txNo,
              },
            },
            onSuccess: (data) => {
              // console.log(`💪💪💪getRewards(${txNo})💪💪💪`);
              let temp = Number(data._hex);
              temp = convertToDecimals(temp);
              rewardForThisTxNo = temp;
              // console.log(`💪💪💪💪💪💪💪💪💪💪💪💪`);
            },
            onError: (err) => {
              console.error(err);
              console.error(`dev-failed to run fn getRewards`);
            },
          });
        } catch (error) {
          console.error(error);
          console.error(`dev-failed to run fn getRewards -trycatch`);
        }
      }
      return rewardForThisTxNo;
    },
    [isAuthenticated, user, isWeb3Enabled, fetch, convertToDecimals]
  );

  const convertToDays = useCallback((totalSeconds) => {
    const secondsInOneDay = 86400;
    let days = totalSeconds / secondsInOneDay;
    days = days < 1 ? 1 : days;
    return days;
  }, []);

  const getUserTransactions = useCallback(
    async (
      userAddress = user
        ? user.get("ethAddress")
        : window.localStorage.getItem("userAddress"),
      txNo = "0"
    ) => {
      if (txNo === "0") {
        console.log(`No transactions to fetch - txNo : ${txNo}`);
        return;
      }

      const fnName = "userTransactions";
      let optionsLocked = {
        abi: ABI_LOCKED_STAKING,
        contractAddress: CONTRACT_ADDRESS_LOCKED_STAKING,
      };

      if (userTransactions.length !== Number(totalStakingTxs)) {
        for (let el = txNo; el > 0; el--) {
          try {
            console.log(`getUserTransactions()🔥🔥🔥`);
            fetch({
              params: {
                ...optionsLocked,
                functionName: fnName,
                params: {
                  _user: userAddress,
                  _txNo: el,
                },
              },
              onSuccess: async (data) => {
                // (data) => {
                // console.log(`--------------------------------`);
                // const rewardForThisTx = "";
                const rewardForThisTx = await getRewards(el);

                function secondsToDhms(seconds) {
                  if (Number(seconds) === 0 || Number(seconds) < 0) {
                    return {
                      day: 0,
                      hr: 0,
                      min: 0,
                      sec: 0,
                    };
                  }
                  seconds = Number(seconds);
                  const secInOneDay = 24 * 60 * 60;
                  const secInOneHour = 60 * 60;
                  let d = Math.floor(seconds / secInOneDay);

                  let secondsLeftAfterDeductingDays = seconds - d * secInOneDay;
                  let h = Math.floor(
                    secondsLeftAfterDeductingDays / secInOneHour
                  );

                  let secondsLeftAfterDeductingDaysAndHours =
                    seconds - (d * secInOneDay + h * secInOneHour);
                  let m = Math.floor(
                    secondsLeftAfterDeductingDaysAndHours / 60
                  );

                  let secondsLeftAfterDeductingDaysAndHoursAndMins =
                    seconds - (d * secInOneDay + h * secInOneHour + m * 60);

                  let s = secondsLeftAfterDeductingDaysAndHoursAndMins;
                  // console.log(`!!!seconds-${seconds}`);
                  // console.log(`d-${d}`);
                  // console.log(`h-${h}`);
                  // console.log(`m-${m}`);
                  // console.log(`s-${s}`);

                  return {
                    day: d,
                    hr: h,
                    min: m,
                    sec: s,
                  };
                }
                const timeDiffInSeconds = unixTimeDiff(
                  Number(data.lockedUntil._hex)
                );
                const tLeftTemp = secondsToDhms(timeDiffInSeconds);
                let canPreClaim = false;
                if (!data.stakingOver && Number(timeDiffInSeconds) !== 0) {
                  canPreClaim = true;
                }
                const stakingTx = {
                  txNo: el,
                  userAddress: user.get("ethAddress"),
                  amount: Number(data.amount._hex),
                  stakingDurationSeconds: Number(data.time._hex),
                  percent: Number(data.percent._hex) / 100,
                  lockedUntil: Number(data.lockedUntil._hex),
                  lockedUntilDate: new Date(
                    Number(data.lockedUntil._hex) * 1000
                  ),
                  isStakingWithdrawn: data.stakingOver,
                  stakingPeriodIsOver: true,
                  reward: rewardForThisTx,
                  timeLeft: tLeftTemp,
                  timeLeftSeconds: timeDiffInSeconds,
                  canPreClaim: canPreClaim,
                  calculatedTimeLeftAt: new Date(),
                };

                /**
                 * If stakingPeriodIsOver than
                 * - add the amount to unlocked rewards
                 * else
                 * - add the amount to locked rewards (locked rewards are dynamic)
                 */

                // setUserTotalRewards((e) =>
                //   Number(Number(e) + Number(stakingTx.reward)).toString()
                // );
                // if (
                //   !stakingTx.isStakingWithdrawn &&
                //   stakingTx.reward &&
                //   !stakingTx.canPreClaim &&
                //   Number(stakingTx.reward) > 0
                // ) {
                //   try {
                //     setUserTotalUnlockedRewards((e) =>
                //       Number(Number(e) + Number(stakingTx.reward)).toString()
                //     );
                //   } catch (error) {
                //     console.error(error);
                //     console.error(
                //       `❌dev-failed setUserTotalUnlockedRewards - bigNumber.`
                //     );
                //   }
                // } else {
                //   try {
                //     setUserTotalLockedRewards((e) =>
                //       Number(Number(e) + Number(stakingTx.reward)).toString()
                //     );
                //   } catch (error) {
                //     console.error(error);
                //     console.error(
                //       `❌dev-failed setUserTotalUnlockedRewards - bigNumber`
                //     );
                //   }
                // }

                if (userTransactions.length < totalStakingTxs) {
                  setUserTransactions((oldTxs) => [...oldTxs, stakingTx]);
                }
                // };
              },
              onError: (err) => {
                console.error(err);
                console.error(`dev-failed to run fn getUserTransactions`);
              },
            });
          } catch (error) {
            console.error(error);
            console.error(`dev-failed to run fn getUserTransactions -trycatch`);
          }
        }
      }
    },

    [user, userTransactionsLoaded, fetch, getRewards, totalStakingTxs]
    // [user, fetch, totalStakingTxs]
  );

  const getTotalIbatStakedinLocked = useCallback(() => {
    if (isAuthenticating || !isAuthenticated || !isWeb3Enabled) return;
    const fnName = "totalstakedamount";
    let optionsLocked = {
      abi: ABI_LOCKED_STAKING,
      contractAddress: CONTRACT_ADDRESS_LOCKED_STAKING,
    };
    try {
      fetch({
        params: {
          ...optionsLocked,
          functionName: fnName,
        },
        onSuccess: (data) => {
          // console.log(convertToDecimals(Number(data._hex)));
          // setTotalIbatStakedinLocked(Number(data._hex));
          const updateTVLInDB = async (newTVL) => {
            // console.log(`🔥🔥🔥update🔥🔥🔥`);
            const lockedTVLReff = doc(db, DB_COLLECTION_NAME, DB_DOC_NAME);
            await updateDoc(lockedTVLReff, {
              tvl: newTVL,
            });
            // console.log(update);
            // console.log(`🔥🔥🔥🔥🔥🔥🔥🔥🔥`);
          };
          updateTVLInDB(convertToDecimals(Number(data._hex)));
          console.log(`dev-✅-TxSuccessful fn getTotalIbatStakedinLocked`);
        },
        onError: (err) => {
          console.error(err);
          console.error(`dev-failed to run fn getTotalIbatStakedinLocked`);
        },
      });
    } catch (error) {
      console.error(error);
      console.error(
        `dev-failed to run fn getTotalIbatStakedinLocked -trycatch`
      );
    }
  }, [
    convertToDecimals,
    db,
    fetch,
    isWeb3Enabled,
    isAuthenticated,
    isAuthenticating,
    DB_COLLECTION_NAME,
  ]);

  const getTotalStakingTxs = useCallback(
    async (userAddress) => {
      if (isAuthenticating || !isAuthenticated || !isWeb3Enabled) return;
      console.log(`ran - getTotalStakingTxs`);
      console.log(`for user`);
      console.log(userAddress);
      let optionsLocked = {
        abi: ABI_LOCKED_STAKING,
        contractAddress: CONTRACT_ADDRESS_LOCKED_STAKING,
      };
      // console.log(user.get("ethAddress"));
      try {
        fetch({
          params: {
            ...optionsLocked,
            functionName: "stakingTx",
            params: {
              "": userAddress,
            },
          },
          onSuccess: (data) => {
            setTotalStakingTxs(Number(data.txNo._hex));
            setUserStakedAmountinLocked(
              Number(data.totalAmount._hex).toString()
            );
            // console.log(`txNo._hex - ${data.txNo._hex}`);
            // console.log(`Number(txNo) - ${Number(data.txNo._hex)}`);
            // console.log(`totalStakingTxs - ${totalStakingTxs}`);
            console.log(`dev-✅-TxSuccessful function getTotalStakingTxs`);
          },
          onError: (err) => {
            console.error(err);
            console.error(`dev-failed to run function getTotalStakingTxs`);
          },
        });
      } catch (error) {
        console.error(error);
        console.error(`dev-failed to run function getTotalStakingTxs-trycatch`);
      }
    },
    [fetch, isWeb3Enabled, isAuthenticated, isAuthenticating]
  );

  useEffect(() => {
    if (isAuthenticated && isWeb3Enabled && user) {
      // console.log(`🎉🎉🎉🎉🎉🎉🎉`);
      getTotalStakingTxs(user.get("ethAddress"));
    }
  }, [getTotalStakingTxs, isAuthenticated, isWeb3Enabled, user]);

  useEffect(() => {
    if (isAuthenticated && isWeb3Enabled && user) {
      getTotalIbatStakedinLocked();
    }
  }, [getTotalIbatStakedinLocked, isAuthenticated, isWeb3Enabled, user]);

  useEffect(() => {
    if (isAuthenticated && isWeb3Enabled && user) {
      setUserTotalRewards("0");
      setUserTotalLockedRewards("0");
      setUserTotalUnlockedRewards("0");
      // console.log(`userTransactions`);
      // console.log(userTransactions);
      userTransactions.forEach((el) => {
        if (el.userAddress === user.get("ethAddress")) {
          // setUserTotalRewards((oldR) =>
          //   (Number(oldR) + Number(el.reward)).toString()
          // );
          /**
           * Calculate Locked Rewards
           * not withdrawn & can be preclaimed
           * timeleft != 0
           *  */
          if (!el.isStakingWithdrawn) {
            //it isn't withdrawn - still staked
            setUserTotalRewards((oldR) =>
              (Number(oldR) + Number(el.reward)).toString()
            );

            if (el.canPreClaim && Number(el.timeLeftSeconds) !== 0) {
              try {
                setUserTotalLockedRewards((e) =>
                  Number(Number(e) + Number(el.reward)).toString()
                );
              } catch (error) {
                console.error(error);
                console.error(
                  `❌dev-failed setUserTotalUnlockedRewards - useEffect`
                );
              }
            }
            if (!el.canPreClaim && Number(el.timeLeftSeconds) === 0) {
              try {
                setUserTotalUnlockedRewards((e) =>
                  Number(Number(e) + Number(el.reward)).toString()
                );
              } catch (error) {
                console.error(error);
                console.error(
                  `❌dev-failed setUserTotalUnlockedRewards - useEffect.`
                );
              }
            }
          }
        }
      });
    }
  }, [isAuthenticated, isWeb3Enabled, user, userTransactions]);

  useEffect(() => {
    if (isAuthenticated && isWeb3Enabled && user) {
      getUserTransactions(user.get("ethAddress"), totalStakingTxs);
    }
  }, [
    getUserTransactions,
    isAuthenticated,
    isWeb3Enabled,
    totalStakingTxs,
    userTransactionsLoaded,
    user,
  ]);

  useEffect(() => {
    if (!isAuthenticated || !user || !isWeb3Enabled) {
      setUserTransactionsLoaded(false);
    }
    if (userTransactions.length === Number(totalStakingTxs)) {
      setUserTransactionsLoaded(true);
    }
  }, [isAuthenticated, isWeb3Enabled, totalStakingTxs, user, userTransactions]);

  // resetting the rewards so that on each re-render the the rewards don't get added to old value
  useEffect(() => {
    setUserTotalRewards("0");
    setUserTotalUnlockedRewards("0");
    setUserTotalLockedRewards("0");
    // }
  }, []);

  const unixTimeDiff = function (untilTimeStamp) {
    const unixTimeNowinSeconds = Math.floor(Date.now() / 1000);
    let tLeft = untilTimeStamp - unixTimeNowinSeconds;
    return tLeft < 0 ? 0 : tLeft;
  };

  const [stakeInputAmount_Locked, setStakeInputAmount_Locked] = useState("0");
  // const [unstakeInputAmount_Locked, setUnstakeInputAmount_Locked] = useState(0);
  const [lockingDuration, setLockingDuration] = useState("0");

  const handleInputAmount = (e, fieldName) => {
    if (!e) {
      console.log(`error with input`);
      return;
    }
    e = Number(e).toFixed(IBAT_DECIMALS);
    try {
      e = Moralis.Units.Token(e, IBAT_DECIMALS);
      // console.log(`${e} - ${fieldName}`);
    } catch (error) {
      console.error(error);
      console.error(`dev-error handleInputAmount()`);
    }

    if (fieldName === "stakeamountlocked") {
      if (e >= 0) {
        setStakeInputAmount_Locked(e);
      }
      return;
    }
  };

  const MIN_DEPOSIT_AMOUNT_WEI = 8000000000000;
  const MIN_DEPOSIT_AMOUNT_IBAT =
    convertToDecimals(MIN_DEPOSIT_AMOUNT_WEI) || 8000;
  // const MIN_DEPOSIT_AMOUNT_IBAT =
  //   Moralis.Units.FromWei(MIN_DEPOSIT_AMOUNT_WEI, IBAT_DECIMALS) || 8000;

  const stakeIbat_Locked = useCallback(async () => {
    console.log(`stakeIbat_Locked()`);
    // let stakeInputAmount_Locked_WEI = Moralis.Units.Token(
    //   stakeInputAmount_Locked,
    //   IBAT_DECIMALS
    // );
    let temp_stakeInputAmount_Locked = Number(stakeInputAmount_Locked).toFixed(
      IBAT_DECIMALS
    );
    console.log(`temp_stakeInputAmount_Locked.toFixed`);
    console.log(temp_stakeInputAmount_Locked);
    try {
      temp_stakeInputAmount_Locked = convertToDecimals(
        stakeInputAmount_Locked
      );
      // temp_stakeInputAmount_Locked = Moralis.Units.FromWei(
      //   stakeInputAmount_Locked,
      //   IBAT_DECIMALS
      // );
    } catch (error) {
      console.error(error);
      console.error(`dev-error stakeIbat_locked - `);
    }
    console.log(`temp_stakeInputAmount_Locked.Moralis`);
    console.log(temp_stakeInputAmount_Locked);

    if (!lockingDuration || lockingDuration === "0") {
      toast({
        containerStyle: {
          maxWidth: "92vw",
        },
        title: `Select locking period`,
        status: "error",
        duration: 1500,
        isClosable: true,
      });
      return;
    }
    console.log(`stakeInputAmount_Locked`);
    console.log(stakeInputAmount_Locked);
    // console.log(`Number(stakeInputAmount_Locked_WEI)`);
    // console.log(Number(stakeInputAmount_Locked_WEI));
    console.log(`Number(MIN_DEPOSIT_AMOUNT_WEI)`);
    console.log(Number(MIN_DEPOSIT_AMOUNT_WEI));
    if (stakeInputAmount_Locked < Number(MIN_DEPOSIT_AMOUNT_WEI)) {
      toast({
        containerStyle: {
          maxWidth: "92vw",
        },
        title: `Staking Amount: ${temp_stakeInputAmount_Locked} IBAT`,
        description: `Minimum stake Amount: ${MIN_DEPOSIT_AMOUNT_IBAT} IBAT`,
        status: "error",
        duration: 1500,
        isClosable: true,
      });
      return;
    }
    // console.log(`Moralis.Units.Token(---totalIbatBalance---, IBAT_DECIMALS)`);
    // console.log(Moralis.Units.Token(totalIbatBalance, IBAT_DECIMALS));

    if (
      Number(stakeInputAmount_Locked) >
      Moralis.Units.Token(totalIbatBalance, IBAT_DECIMALS)
    ) {
      toast({
        containerStyle: {
          maxWidth: "92vw",
        },
        title: "Staking Amount can't be greater than your balance",
        description: `Stake Amount: ${temp_stakeInputAmount_Locked} IBAT`,
        status: "error",
        duration: 2500,
        isClosable: true,
      });
      toast({
        containerStyle: {
          maxWidth: "92vw",
        },
        description: `Your Balance - ${totalIbatBalance}`,
        status: "error",
        duration: 2500,
        isClosable: true,
      });
      return;
    }

    let optionsStake = {
      abi: ABI_LOCKED_STAKING,
      contractAddress: CONTRACT_ADDRESS_LOCKED_STAKING,
      functionName: "stake",
      params: {
        _time: lockingDuration,
        _amount: stakeInputAmount_Locked,
      },
    };

    await fetch({
      params: optionsStake,
      onSuccess: (tx) => {
        toast({
          containerStyle: {
            maxWidth: "92vw",
          },
          description: `Staking Transaction in progress...`,
          status: "info",
          duration: null,
          isClosable: true,
          position: "bottom-left",
        });
        tx.wait().then(async () => {
          try {
            // getTotalIbatStaked();
            // getUserStakedIbat();
            // getIBATBalance();
            setUserTotalUnlockedRewards("0");
            setUserTotalRewards("0");
            setUserTotalLockedRewards("0");

            setTotalStakingTxs(0);
            await getTotalStakingTxs(user.get("ethAddress"));

            setUserTransactions([]);
            await getUserTransactions();

            await getTotalIbatStakedinLocked();
            await console.log(tx);
            toast.closeAll({ positions: ["bottom-left"] });
            toast({
              containerStyle: {
                maxWidth: "92vw",
              },
              description: `Staking Transaction Successful`,
              status: "success",
              duration: "2500",
              isClosable: true,
              position: "bottom-left",
            });
            // console.log(tx.data);
            // getTotalIbatStakedinLocked();
            console.log(`dev-✅-TxSuccessful function stakeIbat_locked`);
          } catch (error) {
            console.error(error);
            console.error(`dev-stakeIbat_Locked`);
          }
        });
      },
      onError: (err) => {
        try {
          if (err && err.data) showGenericErrorMessage(err.data.message);
          else if (err && err.message) showGenericErrorMessage(err.message);
          console.log(err);
          console.error(`dev-failed to run function stakeIbat_locked`);

        } catch (error) {
          console.error(error);
          console.error(`dev-failed onerror fn stakeIbat_locked`);

        }
      },
    });
  }, [
    stakeInputAmount_Locked,
    Moralis.Units,
    lockingDuration,
    totalIbatBalance,
    fetch,
    toast,
    MIN_DEPOSIT_AMOUNT_IBAT,
    getTotalStakingTxs,
    user,
    getUserTransactions,
    getTotalIbatStakedinLocked,
    showGenericErrorMessage,
    convertToDecimals
  ]);

  const { isOpen, onOpen: onOpenPreClaimConfirmation, onClose } = useDisclosure()
  const [preClaimTx, setpreClaimTx] = useState();

  const openModal = useCallback((tx) => {
    setpreClaimTx(tx);
    onOpenPreClaimConfirmation();
  }, [onOpenPreClaimConfirmation])

  const preClaimIBAT = useCallback(
    () => {
      const tx = preClaimTx;
      if (isAuthenticating || !isAuthenticated || !isWeb3Enabled) {
        showGenericErrorMessage("Try again... or Disconnect & Connect again");
        return;
      }
      const fnName = "preClaim";
      let optionsLocked = {
        abi: ABI_LOCKED_STAKING,
        contractAddress: CONTRACT_ADDRESS_LOCKED_STAKING,
      };

      try {
        const txNoToUnstake = tx.txNo;
        fetch({
          params: {
            ...optionsLocked,
            functionName: fnName,
            params: {
              _txNo: txNoToUnstake,
            },
          },
          onSuccess: (txU) => {
            console.log(`dev-✅-submitted fn preClaimIBAT`);

            // console.log(tx);
            // console.log(txU);
            onClose();
            toast({
              containerStyle: {
                maxWidth: "92vw",
              },
              description: `Preclaim tx#${txNoToUnstake} in progress...`,
              status: "info",
              duration: null,
              isClosable: true,
              position: "bottom-left",
            });
            setpreClaimTx('');
            txU.wait().then(async (data) => {
              // console.log(`txU-data`);
              // console.log(data);
              // console.log(`txU`);
              // console.log(txU);

              // Refreshing Values - Start
              setTotalStakingTxs(0);
              await getTotalStakingTxs(
                window.localStorage.getItem("userAddress")
              );

              setUserTransactions([]);
              await getUserTransactions();

              await getTotalIbatStakedinLocked();
              // Refreshing Values - End

              toast.closeAll({ positions: ["bottom-left"] });
              toast({
                containerStyle: {
                  maxWidth: "92vw",
                },
                title: "Successfully Preclaimed",
                description: `Unstaked ${convertToDecimals(
                  tx.amount
                )} IBAT`,
                // description: `Unstaked ${Moralis.Units.FromWei(
                //   tx.amount,
                //   IBAT_DECIMALS
                // )} IBAT`,
                status: "success",
                duration: "2500",
                isClosable: true,
                position: "bottom-left",
              });
              console.log(`dev-✅-TxSuccessful fn preClaimIBAT`);
            });
          },
          onError: (err) => {
            onClose();
            if (err && err.data) showGenericErrorMessage(err.data.message);
            else if (err && err.message) showGenericErrorMessage(err.message);
            console.error(`dev-failed to run fn preClaim - preClaimIBAT`);
          },
        });
      } catch (err) {
        if (err && err.data) showGenericErrorMessage(err.data.message);
        else if (err && err.message) showGenericErrorMessage(err.message);
        console.error(`dev-failed to run fn unstakeIbatClaim -trycatch`);
      }
    },
    [preClaimTx, isAuthenticating, isAuthenticated, isWeb3Enabled, showGenericErrorMessage, fetch, onClose, toast, getTotalStakingTxs, getUserTransactions, getTotalIbatStakedinLocked, convertToDecimals]
  );

  const unstakeIbatClaim = useCallback(
    (tx) => {
      if (isAuthenticating || !isAuthenticated || !isWeb3Enabled) {
        showGenericErrorMessage("Try again... or Disconnect & Connect again");
        return;
      }
      const txNoToUnstake = tx.txNo;
      const fnName = "claim";
      let optionsLocked = {
        abi: ABI_LOCKED_STAKING,
        contractAddress: CONTRACT_ADDRESS_LOCKED_STAKING,
      };
      console.log(`unstaking txNo - ${txNoToUnstake}`);
      try {
        fetch({
          params: {
            ...optionsLocked,
            functionName: fnName,
            params: {
              _txNo: txNoToUnstake,
            },
          },
          onSuccess: (txU) => {
            console.log(`dev-✅-submitted fn unstakeIbatClaim.`);

            console.log(tx);
            console.log(txU);
            toast({
              containerStyle: {
                maxWidth: "92vw",
              },
              description: `Withdraw tx#${txNoToUnstake} in progress...`,
              status: "info",
              duration: null,
              isClosable: true,
              position: "bottom-left",
            });
            txU.wait().then(async (data) => {
              console.log(`txU-data -`);
              console.log(data);
              console.log(`txU`);
              console.log(txU);

              // Refreshing Values - Start
              setTotalStakingTxs(0);
              setUserTransactions([]);

              await getTotalStakingTxs(
                window.localStorage.getItem("userAddress")
              );
              await getUserTransactions();
              await getTotalIbatStakedinLocked();
              // Refreshing Values - End

              toast.closeAll({ positions: ["bottom-left"] });
              toast({
                containerStyle: {
                  maxWidth: "92vw",
                },
                title: `Successfully Unstaked tx#${txNoToUnstake}`,
                description: `Unstaked ${convertToDecimals(
                  tx.amount
                )} IBAT`,
                // description: `Unstaked ${Moralis.Units.FromWei(
                //   tx.amount,
                //   IBAT_DECIMALS
                // )} IBAT`,
                status: "success",
                duration: "2500",
                isClosable: true,
                position: "bottom-left",
              });
              console.log(`dev-✅-TxSuccessful fn unstakeIbatClaim`);
            });
          },
          onError: (err) => {
            console.error(err);
            if (err && err.data) showGenericErrorMessage(err.data.message);
            else if (err && err.message) showGenericErrorMessage(err.message);
            console.error(`dev-failed to run deposit function`);
            console.error(`dev-failed to run fn unstakeIbatClaim`);
          },
        });
      } catch (error) {
        console.error(error);
        console.error(`dev-failed to run fn unstakeIbatClaim -trycatch`);
      }
    },
    [
      fetch,
      getTotalIbatStakedinLocked,
      getTotalStakingTxs,
      getUserTransactions,
      showGenericErrorMessage,
      toast,
      isWeb3Enabled,
      isAuthenticated,
      isAuthenticating,
      convertToDecimals
    ]
    // [fetch, getTotalIbatStakedinLocked, toast] //old values
  );

  const setMaxStakeAmount = () => {
    // deducting 10IBAT from overall amount so that total no. of holders doesn't decrease coz they're staking MAX AMOUNT - making IBAT Holding = 0 IBAT
    const TO_MAINTAIN_WALLET_HOLDERS = 1;
    let e = totalIbatBalance - TO_MAINTAIN_WALLET_HOLDERS;
    console.log(`setMaxStakeAmount`);
    console.log(e);

    let input = document.getElementById("staking__amount_locked");
    setNativeValue(input, e);
    input.dispatchEvent(new Event("input", { bubbles: true }));
  };

  return (
    <>
      <Modal isOpen={isOpen} onClose={onClose} isCentered>
        <ModalOverlay backdropFilter={"blur(5px)"} />
        <ModalContent background={"linear-gradient(90deg, rgba(23, 44, 102, 1) 0%, rgba(24, 22, 82, 1) 100%)"} color="white" borderRadius={"md"}>
          <ModalHeader background={"linear-gradient(45deg, black, transparent)"}>Preclaim Penalty Warning</ModalHeader>
          <ModalCloseButton />
          <ModalBody padding={"1rem"} paddingBottom={"0.5rem !important"}>
            10% tax will be applied if you unlock before staking period ends.
            <Flex direction={"column"} pt=".5rem">
              <Text>Tx.No: {preClaimTx ? preClaimTx.txNo : ''}</Text>
              <Text>Staked Amount: {preClaimTx ? Number(preClaimTx.amount) / 1e9 : ''}</Text>
            </Flex>
          </ModalBody>

          <ModalFooter>
            <Button colorScheme='red' mr={3} onClick={(el) => preClaimIBAT()}>
              Pre-Claim
            </Button>
            <Button background={"transparent"} _hover={{color:"black", background:"#fefefe"}} onClick={onClose}>Cancel</Button>
          </ModalFooter>
        </ModalContent>
      </Modal>
      <div className="staking">
        <h2 className="staking__heading">Locked Staking</h2>
        <div className="staking__tvl">
          <span className="staking__tvl_text">
            Total $IBAT in Locked Staking
          </span>

          <span
            className="staking__tvl_value"
            style={{ color: "#88D1F2", fontWeight: "700" }}
          >
            {!firestoreLoading && firestoreValue && firestoreValue.exists()
              ? Number(firestoreValue.data().tvl).toLocaleString("en-US")
              : "Calculating..."}{" "}
            IBAT
          </span>
        </div>
        {isAuthenticated && (
          <section className="staking__container staking__selector gradient-2">
            <h3 className="staking__selector__heading">Stake IBAT</h3>
            <div>
              <div className="staking__selector--amount">
                <Stack
                  className="staking__amount_input"
                  margin="auto"
                  padding={"1.5rem"}
                >
                  <label
                    className="staking__amount--label"
                    htmlFor="staking__amount"
                  >
                    Amount
                  </label>
                  <Text
                    gap=".5rem"
                    fontSize="xs"
                    className="staking__my_balance"
                  >
                    <span>~My balance: </span>
                    <span>{Number(totalIbatBalance).toFixed(3)} IBAT</span>
                  </Text>
                  <InputGroup>
                    <InputLeftAddon children="$IBAT" />
                    <NumberInput
                      id="staking__amount_locked"
                      className="staking__amount"
                      min={0}
                      placeholder={"0 IBAT"}
                      precision={9}
                      clampValueOnBlur={false}
                      onChange={(e) =>
                        handleInputAmount(e, "stakeamountlocked")
                      }
                    >
                      <NumberInputField borderRadius={0} />
                    </NumberInput>
                    <InputRightAddon
                      padding={0}
                      children={
                        <Button
                          bg="#161838"
                          className="staking__max_btn"
                          onClick={setMaxStakeAmount}
                        >
                          <Text color="white">Max</Text>
                        </Button>
                      }
                    />
                  </InputGroup>
                </Stack>
              </div>
              <div className="staking__selector--time">
                <Stack
                  className="staking__amount_input"
                  margin="auto"
                  padding={"1.5rem"}
                >
                  <label
                    className="staking__amount--label"
                    htmlFor="staking__duration"
                  >
                    Locking Time
                  </label>
                  <DurationSelector
                    setLockingDuration={setLockingDuration}
                    staking_options={STAKING_OPTIONS}
                  />
                </Stack>
              </div>
            </div>
            {lockedAllowance === "true" ? (
              <Button
                className="btn btn--stake-ibat btn__connect_wallet btn-gradient-2"
                onClick={() => stakeIbat_Locked()}
                disabled={isFetching}
              >
                Stake IBAT
              </Button>
            ) : (
              <Button
                className="btn btn--stake-ibat btn__connect_wallet btn-gradient-2"
                onClick={() => enableStaking(CONTRACT_ADDRESS_LOCKED_STAKING)}
                disabled={isFetchingEnableStaking}
              >
                Enable Staking
              </Button>
            )}
            <Text
              fontSize=".75rem"
              width="100%"
              backgroundColor="#181652"
              borderBottomLeftRadius="15px"
              borderBottomRightRadius="15px"
              padding=".5rem"
              paddingBottom=".5rem"
              marginTop="1rem"
              borderTop="1px solid #4c5199"
            >
              {`Locking ${convertToDecimals(
                stakeInputAmount_Locked
              )} IBAT for ${convertToDays(lockingDuration)} Days`}
            </Text>
          </section>
        )}
        {/* Need staking__rewards & staking__history need to be shown after authenticating */}
        {/* <section className="staking__rewards"></section> */}
        {/* <section className="staking__history"></section> */}
        {window.localStorage.getItem("lockedAllowance") === "true" &&
          isAuthenticated && (
            <section className="staking__container staking__rewards gradient-2">
              <h3 className="staking__selector__heading">My IBAT Rewards</h3>
              {/* {userTransactionsLoaded && userTransactions.length > 0 ? ( */}
              <Grid
                templateColumns="repeat(2, 1fr)"
                templateRows="repeat(2, 1fr)"
                columnGap="2"
                rowGap="1rem"
                px="1rem"
                py="1rem"
              >
                <GridItem w="100%" minH="50px">
                  <Text fontSize="xs">Total Staked Amount</Text>

                  {isAuthenticated &&
                    isWeb3Enabled &&
                    (userStakedAmountinLocked !== "0" || !isFetching) ? (
                    <Text fontWeight="700" fontSize="2xl">
                      {/* 
                      Replaced this -
                      {convertToDecimals(userStakedAmountinLocked)}
                      with 
                      {Number(
                        convertToDecimals(userStakedAmountinLocked)
                      ).toFixed(2)}
                        */}
                      {parseFloat(
                        Number(
                          convertToDecimals(userStakedAmountinLocked)
                        ).toFixed(2)
                      )}
                    </Text>
                  ) : (
                    <Skeleton
                      height="1.75rem"
                      width="90%"
                      margin="auto"
                      marginTop="0.5rem"
                    />
                  )}
                </GridItem>
                <GridItem w="100%" minH="50px">
                  <Text fontSize="xs">Total Rewards</Text>
                  <Text fontWeight="700" fontSize="2xl">
                    {/* 
                    Replaced this -
                    Number(userTotalRewards).toFixed(IBAT_DECIMALS)
                    with, 
                    Number(userTotalRewards).toFixed(2)
                    to fix layout
                      */}
                    {parseFloat(Number(userTotalRewards).toFixed(2))}
                  </Text>
                </GridItem>
                <GridItem w="100%" minH="50px">
                  <Text fontSize="xs">Unlocked IBAT Rewards</Text>
                  <Text fontWeight="700" fontSize="2xl">
                    {parseFloat(Number(userTotalUnlockedRewards).toFixed(2))}
                  </Text>
                </GridItem>
                <GridItem w="100%" minH="50px">
                  <Text fontSize="xs">Locked Rewards</Text>
                  <Text fontWeight="700" fontSize="2xl">
                    {parseFloat(Number(userTotalLockedRewards).toFixed(2))}
                  </Text>
                </GridItem>
              </Grid>
              {/* ) : (
                <Text>No transactions</Text>
              )} */}
            </section>
          )}
        {window.localStorage.getItem("lockedAllowance") === "true" &&
          isAuthenticated && (
            <section className="staking__container staking__history gradient-2">
              <h3 className="staking__selector__heading">STAKING HISTORY</h3>
              <Accordion allowMultiple>
                {userTransactionsLoaded && userTransactions.length > 0
                  ? // txNo: el,
                  //   amount: Number(data.amount._hex),
                  //   time: Number(data.time._hex),
                  //   percent: Number(data.percent._hex) / 100,
                  //   lockedUntil: Number(data.lockedUntil._hex),
                  //   stakingOver: data.stakingOver,
                  userTransactions
                    .sort((i, j) => j.txNo - i.txNo)
                    .map((el) =>
                      el.userAddress === user.get("ethAddress") ? (
                        <AccordionItem
                          borderBottom="none"
                          bg=""
                          key={el.txNo}
                        >
                          <AccordionButton
                            _expanded={{
                              color: "white",
                              background:
                                "linear-gradient(90deg, #251F4E 0%, #232E6B 100%)",
                            }}
                          >
                            <Flex w="100%" flexDir="row">
                              <Box textAlign="left" flex="0 0 134px">
                                <Text>Tx.No: {el.txNo}</Text>
                                {/* <Text>22/08/2022</Text> */}
                                <Text fontSize="xs" pt=".5rem" opacity=".8">
                                  {/* Issue here at 18Mil IBAT @todo */}
                                  {convertToDecimals(el.amount)} IBAT Staked
                                </Text>
                              </Box>
                              <Spacer />
                              <Box textAlign="left">
                                <Text fontSize="xs" pt=".5rem" opacity=".8">
                                  Locking Period:{" "}
                                  {convertToDays(el.stakingDurationSeconds)}{" "}
                                  {convertToDays(
                                    el.stakingDurationSeconds
                                  ) === 1
                                    ? "Day"
                                    : "Days"}
                                </Text>
                                <Text fontSize="xs" pt=".5rem" opacity=".8">
                                  {el.isStakingWithdrawn ? (
                                    <Badge
                                      variant="outline"
                                      colorScheme="gray"
                                      px=".5rem"
                                    >
                                      Withdrawn
                                    </Badge>
                                  ) : (
                                    <>
                                      {el.canPreClaim ? (
                                        <Badge
                                          colorScheme="red"
                                          marginRight=".25rem"
                                        >
                                          Locked
                                        </Badge>
                                      ) : (
                                        <Badge
                                          colorScheme="green"
                                          marginRight=".25rem"
                                        >
                                          Unlocked
                                        </Badge>
                                      )}
                                      <Badge colorScheme="red">Staked</Badge>
                                    </>
                                  )}
                                </Text>
                              </Box>
                              <Spacer />
                            </Flex>
                            <AccordionIcon />
                          </AccordionButton>

                          <AccordionPanel
                            pb={4}
                            borderBottom="none"
                            bg="linear-gradient(90deg, #251F4E 0%, #232E6B 100%)"
                            borderBottomRightRadius="13px"
                            borderBottomLeftRadius="15px"
                          >
                            <Flex
                              w="100%"
                              flexDir="row"
                              alignItems="flex-end"
                            >
                              <Box textAlign="left">
                                <Box>
                                  <Box>
                                    <Text
                                      fontSize="xs"
                                      pt=".5rem"
                                      opacity=".8"
                                    >
                                      APY
                                    </Text>
                                    <Text>{el.percent}%</Text>
                                  </Box>
                                  <Box>
                                    <Text
                                      fontSize="xs"
                                      pt=".5rem"
                                      opacity=".8"
                                    >
                                      Rewards:
                                    </Text>
                                    <Text>{el.reward} IBAT</Text>
                                  </Box>
                                </Box>
                                {!el.isStakingWithdrawn && el.canPreClaim && (
                                  <Box mt="1rem">
                                    <Text
                                      fontSize="xs"
                                      pt=".5rem"
                                      opacity=".8"
                                    >
                                      Unlocking Time: (local time)
                                    </Text>
                                    <Text>
                                      {el.lockedUntilDate.toString()}
                                    </Text>
                                    <Text
                                      fontSize="xs"
                                      pt=".5rem"
                                      opacity=".8"
                                    >
                                      Unlocking Time: (UTC)
                                    </Text>
                                    <Text>
                                      {el.lockedUntilDate.toUTCString()}
                                    </Text>
                                  </Box>
                                )}
                              </Box>
                              <Spacer />
                              {el.isStakingWithdrawn ? (
                                ""
                              ) : el.canPreClaim ? (
                                <Button
                                  className="btn__connect_wallet btn__connect_wallet--withdraw btn-gradient-2"
                                  onClick={() => openModal(el)}
                                  // onClick={() => preClaimIBAT(el)}
                                  disabled={isFetching}
                                >
                                  PRE-CLAIM
                                </Button>
                              ) : (
                                <Button
                                  className="btn__connect_wallet btn__connect_wallet--withdraw btn-gradient-2"
                                  onClick={() => unstakeIbatClaim(el)}
                                  disabled={isFetching}
                                >
                                  WITHDRAW
                                </Button>
                              )}
                            </Flex>
                          </AccordionPanel>
                        </AccordionItem>
                      ) : (
                        el.txNo === 1 && "No transactions"
                      )
                    )
                  : "No transactions"}
              </Accordion>
            </section>
          )}
      </div>
    </>
  );
}

export default LockedStaking;
