import { useToast } from "@chakra-ui/react";
import React, { useEffect, useState, useCallback } from "react";
import "./styles/App.scss";
import {
  useMoralis,
  useWeb3ExecuteFunction,
  useMoralisWeb3Api,
} from "react-moralis";

import {
  CONTRACT_ADDRESS_IBAT_TOKEN,
  CONTRACT_ADDRESS_LOCKED_STAKING,
  CONTRACT_ADDRESS_FLEXIBLE_STAKING,
  isTestnet,
} from "./assets/StakingABI";

import { BSC_COINS_SUPPORTED } from "./components/CoinsSupported";
import { panCONTRACT_ADDRESS } from "./assets/PancakeSwapRouterABI";

import Header from "./components/Header";
// eslint-disable-next-line no-unused-vars
import Main from "./components/Main";

// FIREBASE - START
import { initializeApp } from "firebase/app";
import { getFirestore, updateDoc, doc } from "firebase/firestore";
import { useDocument } from "react-firebase-hooks/firestore";
// FIREBASE - END

// ---------- APP -------------
function App() {
  const {
    authenticate,
    isWeb3Enabled,
    isAuthenticated,
    enableWeb3,
    isWeb3EnableLoading,
    Moralis,
    web3,
    user,
  } = useMoralis();

  // Initialize Firebase App - firestore
  const firebaseApp = initializeApp({
    apiKey: "AIzaSyCW_zePbfL7fms_Jg4oRL8Up5OE36rTjcU",
    authDomain: "battleinfinityio.firebaseapp.com",
    projectId: "battleinfinityio",
    storageBucket: "battleinfinityio.appspot.com",
    messagingSenderId: "694775551331",
    appId: "1:694775551331:web:75351fa92c161a0a1daf56",
  });

  const { fetch } = useWeb3ExecuteFunction();
  const [flexAllowance, setFlexAllowance] = useState(false);
  const [lockedAllowance, setLockedAllowance] = useState(false);
  const [totalIbatStaked, setTotalIbatStaked] = useState(0);
  const toast = useToast();

  // eslint-disable-next-line react-hooks/exhaustive-deps
  useEffect(() => {
    setFlexAllowance(window.localStorage.getItem("flexAllowance"));
    setLockedAllowance(window.localStorage.getItem("lockedAllowance"));
  });

  useEffect(() => {
    console.log(`---- verifying value ----`);
    console.log(`isTestnet`);
    console.log(isTestnet);
    console.log(`CONTRACT_ADDRESS_FLEXIBLE_STAKING`);
    console.log(CONTRACT_ADDRESS_FLEXIBLE_STAKING);
    console.log(`CONTRACT_ADDRESS_LOCKED_STAKING`);
    console.log(CONTRACT_ADDRESS_LOCKED_STAKING);
    console.log(`CONTRACT_ADDRESS_IBAT_TOKEN`);
    console.log(CONTRACT_ADDRESS_IBAT_TOKEN);
    console.log(`---------------------------`);
  }, []);

  useEffect(() => {
    // console.log(`isAuthenticated - ${isAuthenticated}`);
    // console.log(`isWeb3Enabled - ${isWeb3Enabled}`);
    // console.log(`isWeb3EnableLoading - ${isWeb3EnableLoading}`);
    if (!isWeb3EnableLoading) {
      if (isAuthenticated && !isWeb3Enabled) {
        !window.ethereum
          ? enableWeb3({ provider: "walletconnect" })
          : enableWeb3();
      }
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [isAuthenticated, enableWeb3, isWeb3Enabled, web3]);

  useEffect(() => {
    if (isAuthenticated && !isWeb3Enabled) {
      if (!toast.isActive("1")) {
        toast({
          id: "1",
          title: `Web3 Disconnected :(`,
          description: `Disconnet & Connect Wallet again`,
          status: "error",
          duration: null,
          isClosable: true,
          position: "bottom-left",
          containerStyle: {
            maxWidth: "92vw",
          },
        });
      }
    }
  }, [isAuthenticated, isWeb3Enabled, toast]);
  useEffect(() => {
    if (isAuthenticated && isWeb3Enabled) {
      toast.closeAll({ positions: ["bottom-left"] });
      toast({
        title: `Web3 Connected`,
        status: "success",
        duration: "3000",
        isClosable: true,
        position: "bottom-left",
        containerStyle: {
          maxWidth: "92vw",
        },
      });
    }
  }, [isAuthenticated, isWeb3Enabled, toast]);

  const showGenericErrorMessage = useCallback(
    (msg) => {
      toast({
        title: `Oops... something went wrong!`,
        description: msg,
        status: "error",
        duration: 2000,
        isClosable: true,
        position: "bottom-right",
        containerStyle: {
          maxWidth: "92vw",
        },
      });
    },
    [toast]
  );

  const getTotalIbatStaked = useCallback(async () => {
    // make sure that amount input is more than minimum
    const ABI_total_staked = [
      {
        inputs: [],
        name: "total_staked",
        outputs: [{ internalType: "uint256", name: "", type: "uint256" }],
        stateMutability: "view",
        type: "function",
      },
    ];
    let optionsTotalStaked = {
      abi: ABI_total_staked,
      contractAddress: CONTRACT_ADDRESS_FLEXIBLE_STAKING,
      functionName: "total_staked",
    };
    await fetch({
      params: optionsTotalStaked,
      onError: (err) => {
        console.log(err);
        console.error(`dev:failed to getTotalIbatStaked`);
      },
      onSuccess: (data) => {
        let amount = Number(data._hex) / Math.pow(10, 9);
        amount = amount.toFixed(0);
        setTotalIbatStaked(amount);
      },
    });
  }, [fetch]);

  async function hasAllowance(userAddress, contractAddress) {
    // if (!userAddress) userAddress = user.get("ethAddress");
    // console.log(`===RUNNING hasAllowance===`);
    // console.log(`userAddress -${userAddress}`);
    // console.log(`contractAddress -${contractAddress}`);
    // let userAddress = "0xC8179e6927b61A4FdC3e5a2dB14e641E51b9ad83";
    // userAddress = "0x43af3a4f4d391ea3b5c2143a160baaf512de7d25";
    let ABI_allowance = [
      {
        inputs: [
          { internalType: "address", name: "owner", type: "address" },
          { internalType: "address", name: "spender", type: "address" },
        ],
        name: "allowance",
        outputs: [{ internalType: "uint256", name: "", type: "uint256" }],
        stateMutability: "view",
        type: "function",
      },
    ];
    let optionsAllowance = {
      abi: ABI_allowance,
      contractAddress: CONTRACT_ADDRESS_IBAT_TOKEN,
      functionName: "allowance",
      params: {
        owner: userAddress,
        spender: contractAddress,
      },
    };

    // console.log(`running fetch`);
    await fetch({
      params: optionsAllowance,
      onSuccess: (data) => {
        if (contractAddress === CONTRACT_ADDRESS_FLEXIBLE_STAKING) {
          if (Number(data._hex) !== 0) {
            window.localStorage.setItem("flexAllowance", "true");
            // console.log(`dev-✅-Ran function flexAllowance successfully`);
          } else {
            window.localStorage.setItem("flexAllowance", "false");
          }
        }
        if (contractAddress === CONTRACT_ADDRESS_LOCKED_STAKING) {
          if (Number(data._hex) !== 0) {
            window.localStorage.setItem("lockedAllowance", "true");
            // console.log(`dev-✅-Ran function lockedAllowance successfully`);
          } else {
            window.localStorage.setItem("lockedAllowance", "false");
          }
        }
      },
      onError: (err) => {
        console.error(err);
        window.localStorage.setItem("flexAllowance", "false");
        window.localStorage.setItem("lockedAllowance", "false");
        console.error(`dev-❌failed to run allowance function`);
      },
    });
    // console.log(`ran fetch`);
  }

  const login = async (provider) => {
    if (!isAuthenticated) {
      let battleSigningMessage = "Battle Infinity Authentication";
      if (!provider) {
        try {
          await authenticate({
            signingMessage: battleSigningMessage,
            onSuccess: (tx) => {
              console.log(`⚡⚡⚡ Authenticated ⚡⚡⚡`);
              console.log(tx);
              console.log(`⚡⚡⚡⚡⚡⚡⚡⚡⚡⚡⚡⚡`);
            },
            onError: (err) => {
              console.error(err);
              console.error(err.message);
              showGenericErrorMessage(err.message);
            },
          }).then((user) => {
            console.log("logged in user MM:", user);
            user && console.log(user.get("ethAddress"));

            toast({
              title: `Wallet Connected Successfully`,
              description: `Address - ${user.get("ethAddress")}`,
              status: "success",
              duration: 3000,
              isClosable: true,
              containerStyle: {
                maxWidth: "92vw",
              },
            });
            if (Moralis.getChainId() !== "0x38") {
              toast({
                description: `Connected to Wrong Chain. \nSwitch to BSC Mainnet.`,
                status: "error",
                duration: 2500,
                isClosable: true,
                containerStyle: {
                  maxWidth: "92vw",
                },
              });
            }

            // initial setup functions
            user &&
              hasAllowance(
                user.get("ethAddress"),
                CONTRACT_ADDRESS_FLEXIBLE_STAKING
              );
            user &&
              hasAllowance(
                user.get("ethAddress"),
                CONTRACT_ADDRESS_LOCKED_STAKING
              );
            user &&
              window.localStorage.setItem(
                "userAddress",
                user.get("ethAddress")
              );

            getTotalIbatStaked();
            try {
              console.log(`fetchTokenPrice()`);
              fetchTokenPrice();
            } catch (error) {
              console.error(error);
              console.error(`dev-failed fetchTokenPrice()`);
            }
          });
          // .catch((error) => {
          //   console.log(`MM-- ${error[0]}`);
          //   console.log(`-------`);
          //   console.log(error);
          //   console.log(`-------`);

          //   console.error(`dev-failed to authenticate`);
          // });
        } catch (error) {
          // console.log(`MM-- ${error[0]}`);
          // console.log(`-------`);
          // console.log(error);
          console.log(`-------`);
          // console.error(`dev-failed to authenticate`);
        }
        window.ethereum.on("message", (message) => {
          console.log(`-------`);
          console.log(`❌❌❌❌`);
          console.log(`-------`);
          // genericErrorMessage(message);
        });
      } else if (provider === "walletconnect") {
        console.log(`WC - ${provider === "walletconnect"}`);
        console.log(`Logging with ${provider} - app`);
        await authenticate({
          provider: "walletconnect",
          chainId: 56,
          signingMessage: battleSigningMessage,
        }).then((user) => {
          toast({
            title: `Wallet Connected Successfully`,
            description: `Address - ${user.get("ethAddress")}`,
            status: "success",
            duration: 1500,
            isClosable: true,
            containerStyle: {
              maxWidth: "92vw",
            },
          });

          console.log(`isAuthenticated - ${isAuthenticated}.`);
          console.log("logged in user WC:", user);
          console.log(user.get("ethAddress"));
          console.log(`isAuthenticated - ${isAuthenticated}.`);
          user && console.log(user.get("ethAddress"));

          // initial setup functions
          hasAllowance(
            user.get("ethAddress"),
            CONTRACT_ADDRESS_FLEXIBLE_STAKING
          );
          hasAllowance(user.get("ethAddress"), CONTRACT_ADDRESS_LOCKED_STAKING);
          user &&
            window.localStorage.setItem("userAddress", user.get("ethAddress"));
          getTotalIbatStaked();
        });

        try {
          console.log(`fetchTokenPrice()`);
          await fetchTokenPrice();
        } catch (error) {
          console.error(error);
          console.error(`dev-failed fetchTokenPrice()`);
        }
        // try {
        //   console.log("logged in user:", user);
        //   user && console.log(user.get("ethAddress"));
        // } catch (error) {
        //   console.log(error);
        //   console.error(`dev-failed to authenticate with walletconnect`);
        // }
      }
    }
  };

  /**
   * Fetch Token Price
   */
  const Web3Api = useMoralisWeb3Api();
  const db = getFirestore(firebaseApp);
  const DB_COLLECTION_NAME = "token";
  const DB_DOC_NAME = "price";
  const tokenPriceUSDRef = doc(db, DB_COLLECTION_NAME, DB_DOC_NAME);
  const [firestoreValue, firestoreLoading] = useDocument(tokenPriceUSDRef, {
    snapshotListenOptions: { includeMetadataChanges: true },
  });

  const [tokenPriceUSD, setTokenPriceUSD] = useState("");

  const updateTokenPriceUSDDB = useCallback(
    async (priceData) => {
      await updateDoc(tokenPriceUSDRef, {
        usdPrice: Number(priceData.usdPrice).toFixed(5),
        priceData: priceData,
      });
    },
    [tokenPriceUSDRef]
  );

  // //Get tokenPriceUSD from PancakeSwap v2 BSC & update it in firestore
  const fetchTokenPrice = useCallback(async () => {
    const options = {
      address: "0x19cd9b8e42d4ef62c3ea124110d5cfd283ceac43",
      chain: "bsc",
      exchange: "PancakeSwapv2",
    };
    if (isWeb3Enabled) {
      console.log(`🤙🤙🤙 Called fetchTokenPrice() 🤙🤙🤙`);
      const priceData = await Web3Api.token.getTokenPrice(options);
      updateTokenPriceUSDDB(priceData);
    } else {
      console.log(
        `❌ Failed to call fetchTokenPrice() coz isWeb3Enabled = ${isWeb3Enabled} ❌`
      );
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [Web3Api.token, isWeb3Enabled]);

  useEffect(() => {
    isWeb3Enabled && fetchTokenPrice();
  }, [isWeb3Enabled, fetchTokenPrice]);

  // get tokenPriceUSD from firestore
  useEffect(() => {
    let tokenPrice = 0;
    !firestoreLoading &&
      firestoreValue &&
      firestoreValue.exists() &&
      (tokenPrice = firestoreValue.data().usdPrice);
    setTokenPriceUSD(tokenPrice);
  }, [firestoreLoading, firestoreValue]);
  /****************/

  const hasAllowance2 = useCallback(async (userAddress, spenderContractAddress, tokenAddressToCheck) => {
    let ABI_allowance = BSC_COINS_SUPPORTED.filter(e => e.bscContractAddress.toUpperCase() === tokenAddressToCheck.toUpperCase())[0].ABI_allowance;
    tokenAddressToCheck = tokenAddressToCheck && tokenAddressToCheck.length > 0 ? tokenAddressToCheck : CONTRACT_ADDRESS_IBAT_TOKEN;

    let optionsAllowance = {
      abi: ABI_allowance,
      contractAddress: tokenAddressToCheck,
      functionName: "allowance",
      params: {
        owner: userAddress,
        spender: spenderContractAddress,
      },
    };
    // console.log(`optionsAllowance - ${tokenAddressToCheck}`);
    // console.log(optionsAllowance);
    let hasAl = false;
    await fetch({
      params: optionsAllowance,
      onSuccess: (data) => {
        if (Number(data._hex) !== 0) {
          // console.log(`✅allowance - ${tokenAddressToCheck}`);
          hasAl = true;
        }
      },
      onError: (err) => {
        console.error(err);
        console.error(`dev-❌failed to run allowance function`);
      },
    });
    return hasAl;
  }, [fetch])

  useEffect(() => {
    if (!isWeb3Enabled || !isAuthenticated) return;

    BSC_COINS_SUPPORTED.forEach((token) => {
      if (token.symbol.toUpperCase() !== "BNB") {
        hasAllowance2(user.get("ethAddress"), panCONTRACT_ADDRESS, token.bscContractAddress).then((hasAl) => {
          console.log(`${token.symbol} - swap allowance - ${hasAl}`);
          if (hasAl) window.localStorage.setItem(`${token.symbol.toUpperCase()}allowance`, "true");
          else window.localStorage.setItem(`${token.symbol.toUpperCase()}allowance`, "false");
        });
      }
    })
    window.localStorage.setItem(`BNBallowance`, "true");
    // tokenAllowance removed when logging out - window.localStorage.removeItem
  }, [isWeb3Enabled, isAuthenticated, user, hasAllowance2]);

  return (
    <div className="app">
      <Header login={login} hasAllowance={hasAllowance}></Header>
      <Main
        login={login}
        flexAllowance={flexAllowance}
        setFlexAllowance={setFlexAllowance}
        lockedAllowance={lockedAllowance}
        setLockedAllowance={setLockedAllowance}
        totalIbatStaked={totalIbatStaked}
        setTotalIbatStaked={setTotalIbatStaked}
        getTotalIbatStaked={getTotalIbatStaked}
        showGenericErrorMessage={showGenericErrorMessage}
        firebaseApp={firebaseApp}
        tokenPriceUSD={tokenPriceUSD}
        firestoreUSDPriceLoading={firestoreLoading}
      ></Main>
    </div>
  );
}

export default App;
