import {
  Button,
  NumberInput,
  NumberInputField,
  InputGroup,
  InputLeftAddon,
  InputRightAddon,
  Text,
  Stack,
  Grid,
  GridItem,
  Flex,
  Show,
  Tabs,
  TabList,
  TabPanels,
  Tab,
  TabPanel,
  useToast,
} from "@chakra-ui/react";
import "../styles/Staking.scss";
import "../styles/Buttons.scss";
import { useMoralis, useWeb3ExecuteFunction } from "react-moralis";
import { useState, useEffect, useCallback } from "react";
import iconRefresh from "../assets/refresh.svg";
// FIREBASE - START
import { getFirestore, updateDoc, doc } from "firebase/firestore";
import { useDocument } from "react-firebase-hooks/firestore";
// FIREBASE - END

import {
  CONTRACT_ADDRESS_FLEXIBLE_STAKING,
  IBAT_DECIMALS,
  firebaseCol,
} from "../assets/StakingABI";

function FlexibleStaking({
  flexAllowance,
  enableStaking,
  isFetchingEnableStaking,
  firebaseApp,
  convertToDecimals,
  getIBATBalance,
  totalIbatBalance,
  setNativeValue,
  showGenericErrorMessage,
}) {
  const { isAuthenticated, isAuthenticating, isWeb3Enabled, user, Moralis } =
    useMoralis();
  const { fetch, isFetching } = useWeb3ExecuteFunction();

  const toast = useToast();
  // const bigNum = Moralis.web3Library.BigNumber;

  const [userStakedIbat, setUserStakedIbat] = useState("0");
  const [userReward, setUserReward] = useState("0");

  const [stakeInputAmount, setStakeInputAmount] = useState(0);
  const [unstakeInputAmount, setUnstakeInputAmount] = useState(0);

  /**
   * START
   * Firebase stuff
   */
  const db = getFirestore(firebaseApp);
  const DB_COLLECTION_NAME = firebaseCol;
  const DB_DOC_NAME = "flexible";
  const lockedTVLRef = doc(db, DB_COLLECTION_NAME, DB_DOC_NAME);

  const [firestoreValue, firestoreLoading] = useDocument(lockedTVLRef, {
    snapshotListenOptions: { includeMetadataChanges: true },
  });

  /**
   * Firebase stuff
   * END
   */

  const getTotalIbatStaked = useCallback(async () => {
    // make sure that amount input is more than minimum
    const ABI_total_staked = [
      {
        inputs: [],
        name: "total_staked",
        outputs: [{ internalType: "uint256", name: "", type: "uint256" }],
        stateMutability: "view",
        type: "function",
      },
    ];
    let optionsTotalStaked = {
      abi: ABI_total_staked,
      contractAddress: CONTRACT_ADDRESS_FLEXIBLE_STAKING,
      functionName: "total_staked",
    };
    await fetch({
      params: optionsTotalStaked,
      onError: (err) => {
        console.error(err);
        console.error(`dev:failed to getTotalIbatStaked()`);
      },
      onSuccess: (data) => {
        const updateTVLInDB = async (newTVL) => {
          const lockedTVLReff = doc(db, DB_COLLECTION_NAME, DB_DOC_NAME);
          await updateDoc(lockedTVLReff, {
            tvl: newTVL,
          });
        };
        let temp = 0;
        temp = convertToDecimals(Number(data._hex));
        updateTVLInDB(temp);
        console.log(`dev-✅-TxSuccessful function getTotalIbatStaked`);
      },
    });
  }, [DB_COLLECTION_NAME, convertToDecimals, db, fetch]);

  const handleInputAmount = (e, fieldName) => {
    if (!e) {
      console.log(`error with input`);
      return;
    }
    e = Number(e).toFixed(IBAT_DECIMALS);
    try {
      e = Moralis.Units.Token(e, IBAT_DECIMALS);
      // console.log(`${e} - ${fieldName}`);
    } catch (error) {
      console.error(error);
      console.error(`dev-error handleInputAmount()`);
    }

    if (fieldName === "stakeamount") {
      if (e >= 0) {
        setStakeInputAmount(e);
      }
      return;
    }
    if (fieldName === "unstakeamount") {
      if (e >= 0) setUnstakeInputAmount(e);
      return;
    }
  };

  const withdrawRewards = async (amount) => {
    if (isAuthenticating || !isAuthenticated) return;
    amount = Number(amount);
    console.log(`claim rewards - withdrawRewards()`);
    const ABI_claimRewards = [
      {
        inputs: [],
        name: "claimRewards",
        outputs: [],
        stateMutability: "nonpayable",
        type: "function",
      },
    ];

    let options_claimRewards = {
      abi: ABI_claimRewards,
      contractAddress: CONTRACT_ADDRESS_FLEXIBLE_STAKING,
      functionName: "claimRewards",
    };

    if (isAuthenticated) {
      await fetch({
        params: options_claimRewards,
        onError: (err) => {
          console.log(err);
          if (err && err.data) showGenericErrorMessage(err.data.message);
          else if (err && err.message) showGenericErrorMessage(err.message);
          console.error(`dev:failed to claimRewards`);
        },
        onSuccess: (tx) => {
          toast({
            containerStyle: {
              maxWidth: "92vw",
            },
            description: `Withdraw Rewards Tx. in progress...`,
            status: "info",
            duration: null,
            isClosable: true,
            position: "bottom-left",
          });
          tx.wait().then((data) => {
            console.log(data);
            console.log(`✅claimRewards- updating values`);
            getTotalIbatStaked();
            if (isAuthenticated && user) {
              getUserStakedIbat();
              getIBATBalance();
            }
            toast.closeAll({ positions: ["bottom-left"] });
            toast({
              containerStyle: {
                maxWidth: "92vw",
              },
              title: "Rewards Claimed Successfully",
              status: "success",
              duration: 2500,
              isClosable: true,
              position: "bottom-left",
            });
            console.log(`✅claimRewards successful`);
          });
        },
      });
    }
  };

  const timeLeftForStakingRewards = async () => {
    if (isAuthenticating || !isAuthenticated) return;
    console.log(`Getting timeLeftForStakingRewards`);
    const ABI_compoundRewardsTimer = [
      {
        inputs: [{ internalType: "address", name: "_user", type: "address" }],
        name: "compoundRewardsTimer",
        outputs: [{ internalType: "uint256", name: "_timer", type: "uint256" }],
        stateMutability: "view",
        type: "function",
      },
    ];
    let userAddress = user
      ? user.get("ethAddress")
      : window.localStorage.getItem("userAddress");
    let options_compoundRewardsTimer = {
      abi: ABI_compoundRewardsTimer,
      contractAddress: CONTRACT_ADDRESS_FLEXIBLE_STAKING,
      functionName: "compoundRewardsTimer",
      params: {
        _user: userAddress,
      },
    };

    if (isAuthenticated) {
      await fetch({
        params: options_compoundRewardsTimer,
        onError: (err) => {
          console.error(err);
          console.error(`dev:failed to timeLeftForStakingRewards`);
        },
        onSuccess: (data) => {
          function convertHMS(value) {
            const sec = parseInt(value, 10); // convert value to number if it's string
            let hours = Math.floor(sec / 3600); // get hours
            let minutes = Math.floor((sec - hours * 3600) / 60); // get minutes
            let seconds = sec - hours * 3600 - minutes * 60; //  get seconds
            // add 0 if value < 10; Example: 2 => 02
            if (hours < 10) {
              hours = "0" + hours;
            }
            if (minutes < 10) {
              minutes = "0" + minutes;
            }
            if (seconds < 10) {
              seconds = "0" + seconds;
            }

            if (hours === 0 && minutes === 0) {
              toast({
                containerStyle: {
                  maxWidth: "92vw",
                },
                title: `${seconds} seconds`,
                description: `Time life until restaking`,
                status: "info",
                duration: 1500,
                isClosable: true,
              });
            } else if (hours === 0 && minutes > 0) {
              toast({
                containerStyle: {
                  maxWidth: "92vw",
                },
                title: `${minutes} min : ${seconds} seconds`,
                description: `Time life until restaking`,
                status: "info",
                duration: 1500,
                isClosable: true,
              });
            } else {
              toast({
                containerStyle: {
                  maxWidth: "92vw",
                },
                title: `${hours} hr :${minutes} min`,
                description: `Time life until restaking`,
                status: "info",
                duration: 1500,
                isClosable: true,
              });
            }
            const timeLeft = { hr: hours, min: minutes, sec: seconds };
            return timeLeft;
          }
          convertHMS(Number(data._hex));
        },
      });
    }
  };

  const stakeMyRewards = async () => {
    if (isAuthenticating || !isAuthenticated) return;
    const ABI_stakeMyRewards = [
      {
        inputs: [],
        name: "stakeRewards",
        outputs: [],
        stateMutability: "nonpayable",
        type: "function",
      },
    ];
    let options_stakeMyRewards = {
      abi: ABI_stakeMyRewards,
      contractAddress: CONTRACT_ADDRESS_FLEXIBLE_STAKING,
      functionName: "stakeRewards",
    };

    if (isAuthenticated) {
      await fetch({
        params: options_stakeMyRewards,
        onError: async (err) => {
          console.error(err);
          console.error(`dev:failed to stakeMyRewards`);
          await timeLeftForStakingRewards();
        },
        onSuccess: (data) => {
          console.log(data);
          getTotalIbatStaked();
          getUserStakedIbat();
          getIBATBalance();
          console.log(`✅staked rewards successfully`);
        },
      });
    }
  };

  const getUserStakedIbat = useCallback(async () => {
    if (isAuthenticating || !isAuthenticated) return;
    const ABI_getDepositInfo = [
      {
        inputs: [{ internalType: "address", name: "_user", type: "address" }],
        name: "getDepositInfo",
        outputs: [
          { internalType: "uint256", name: "_stake", type: "uint256" },
          { internalType: "uint256", name: "_rewards", type: "uint256" },
        ],
        stateMutability: "view",
        type: "function",
      },
    ];
    let userAddress = user
      ? user.get("ethAddress")
      : window.localStorage.getItem("userAddress");
    let options_getDepositInfo = {
      abi: ABI_getDepositInfo,
      contractAddress: CONTRACT_ADDRESS_FLEXIBLE_STAKING,
      functionName: "getDepositInfo",
      params: {
        _user: userAddress,
      },
    };

    if (userAddress !== undefined && isAuthenticated) {
      await fetch({
        params: options_getDepositInfo,
        onError: (err) => {
          console.error(err);
          console.error(`dev:failed to getUserStakedIbat-fetch`);
        },

        onSuccess: (data) => {
          let tempWEI = Number(data._stake._hex);

          let temp = 0;
          try {
            temp = Moralis.Units.FromWei(tempWEI, IBAT_DECIMALS);
          } catch (error) {
            temp =
              Number(tempWEI).toFixed(IBAT_DECIMALS) /
              Math.pow(10, IBAT_DECIMALS);
            console.error(
              `dev:getUserStakedIbat() value too big for Moralis Lib - so failed and used toFixed`
            );
          }

          setUserStakedIbat(temp);
          // console.log(`setUserReward - ${Number(data._rewards._hex)} `);
          // console.log(
          //   `setUserReward-toString - ${Number(data._rewards._hex).toString()} `
          // );
          // console.log(`setUserReward-hex - ${data._rewards._hex} `);
          setUserReward(Number(data._rewards._hex).toString());
          // console.log(`flexible-userStakedIbatWEI - ${tempWEI}`);
          // console.log(
          //   `flexible-userRewardIbatWEI - ${Number(
          //     data._rewards._hex
          //   ).toString()}`
          // );
          // // console.log(`data`);
          // console.log(data);
          // console.log(`options_getDepositInfo`);
          // console.log(options_getDepositInfo);
          console.log(`dev-✅- function getUserStakedIbat`);
        },
      });
    }
  }, [isAuthenticating, isAuthenticated, user, fetch, Moralis.Units]);

  useEffect(() => {
    if (isAuthenticated && isWeb3Enabled && user) {
      getTotalIbatStaked();
    }
  }, [getTotalIbatStaked, isAuthenticated, isWeb3Enabled, user]);

  useEffect(() => {
    if (isAuthenticated && isWeb3Enabled && user) {
      getIBATBalance();
    }
  }, [getIBATBalance, isAuthenticated, isWeb3Enabled, user]);

  useEffect(() => {
    if (isAuthenticated && isWeb3Enabled && user) {
      getUserStakedIbat();
    }
  }, [getUserStakedIbat, isAuthenticated, isWeb3Enabled, user]);

  // useEffect(() => {
  //   const interval = setInterval(() => {
  //     getUserStakedIbat();
  //   }, 15000);

  //   return () => clearInterval(interval);
  // }, []);

  const MIN_DEPOSIT_AMOUNT = 8000000000000;
  const MIN_DEPOSIT_AMOUNT_IBAT =
    Moralis.Units.FromWei(MIN_DEPOSIT_AMOUNT, IBAT_DECIMALS) || 8000;

  const stakeIbat = async (depositAmount) => {
    depositAmount = stakeInputAmount;

    if (depositAmount < MIN_DEPOSIT_AMOUNT) {
      toast({
        containerStyle: {
          maxWidth: "92vw",
        },
        title: `Staking Amount: ${Moralis.Units.FromWei(
          depositAmount,
          IBAT_DECIMALS
        )} IBAT`,
        description: `Minimum stake Amount: ${MIN_DEPOSIT_AMOUNT_IBAT} IBAT`,
        status: "error",
        duration: 1500,
        isClosable: true,
      });
      return;
    }

    if (
      Number(depositAmount) >
      Moralis.Units.Token(totalIbatBalance, IBAT_DECIMALS)
    ) {
      toast({
        containerStyle: {
          maxWidth: "92vw",
        },
        title: "Staking Amount can't be greater than your balance",
        description: `Stake Amount: ${Moralis.Units.FromWei(
          depositAmount,
          IBAT_DECIMALS
        )} IBAT`,
        status: "error",
        duration: 2500,
        isClosable: true,
      });
      toast({
        containerStyle: {
          maxWidth: "92vw",
        },
        description: `Your Balance - ${totalIbatBalance}`,
        status: "error",
        duration: 2500,
        isClosable: true,
      });
      return;
    }
    console.log(`Depositing depositAmount - ${depositAmount}`);

    const ABI_deposit = [
      {
        inputs: [{ internalType: "uint256", name: "_amount", type: "uint256" }],
        name: "deposit",
        outputs: [],
        stateMutability: "nonpayable",
        type: "function",
      },
    ];
    let optionsDeposit = {
      abi: ABI_deposit,
      contractAddress: CONTRACT_ADDRESS_FLEXIBLE_STAKING,
      functionName: "deposit",
      params: {
        _amount: depositAmount,
      },
    };
    await fetch({
      params: optionsDeposit,
      onSuccess: (tx) => {
        toast({
          containerStyle: {
            maxWidth: "92vw",
          },
          description: `Staking Transaction in progress...`,
          status: "info",
          duration: null,
          isClosable: true,
          position: "bottom-left",
        });
        tx.wait().then(() => {
          getTotalIbatStaked();
          getUserStakedIbat();
          getIBATBalance();
          toast.closeAll({ positions: ["bottom-left"] });
          toast({
            containerStyle: {
              maxWidth: "92vw",
            },
            description: `Staking Transaction Successful`,
            status: "success",
            duration: "2500",
            isClosable: true,
            position: "bottom-left",
          });
          console.log(`dev-✅-TxSuccessful function deposit`);
        });
      },
      onError: (err) => {
        // console.log(err);
        if (err && err.data) showGenericErrorMessage(err.data.message);
        else if (err && err.message) showGenericErrorMessage(err.message);
        console.error(`dev-failed to run deposit function`);
      },
    });
  };
  const unstakeIbat = async (withdrawAmount) => {
    withdrawAmount = unstakeInputAmount;
    if (Number(withdrawAmount) === 0) {
      toast({
        containerStyle: {
          maxWidth: "92vw",
        },
        description: `Unstaking Amount can not be 0 IBAT.`,
        status: "error",
        duration: "2500",
        isClosable: true,
      });
      return;
    }
    console.log(`unstakeIbat withdrawAmount - ${withdrawAmount} `);
    if (withdrawAmount <= 0) {
      showGenericErrorMessage(`Unstake Amount should be larger than 0 IBAT`);
      return;
    }

    // make sure that amount input is more than minimum
    const ABI_withdraw = [
      {
        inputs: [{ internalType: "uint256", name: "_amount", type: "uint256" }],
        name: "withdraw",
        outputs: [],
        stateMutability: "nonpayable",
        type: "function",
      },
    ];
    let options = {
      abi: ABI_withdraw,
      contractAddress: CONTRACT_ADDRESS_FLEXIBLE_STAKING,
      functionName: "withdraw",
      params: {
        _amount: withdrawAmount,
      },
    };
    await fetch({
      params: options,
      onSuccess: (tx) => {
        toast({
          containerStyle: {
            maxWidth: "92vw",
          },
          description: `Un-Staking Transaction in progress...`,
          status: "info",
          duration: null,
          isClosable: true,
          position: "bottom-left",
        });
        tx.wait().then(() => {
          getTotalIbatStaked();
          getUserStakedIbat();
          getIBATBalance();
          toast.closeAll({ positions: ["bottom-left"] });
          toast({
            containerStyle: {
              maxWidth: "92vw",
            },
            description: `Un-Staking Transaction Successful`,
            status: "success",
            duration: "2500",
            isClosable: true,
            position: "bottom-left",
          });
          console.log(`dev-✅-function unstakeIbat`);
        });
      },
      onError: (err) => {
        // console.log(err);
        if (err && err.data) showGenericErrorMessage(err.data.message);
        else if (err && err.message) showGenericErrorMessage(err.message);
        console.error(`dev-failed to run unstakeIbat function`);
      },
    });
  };

  const setMaxStakeAmount = () => {
    const TO_MAINTAIN_WALLET_HOLDERS = 1;
    let maxAmount = totalIbatBalance - TO_MAINTAIN_WALLET_HOLDERS;
    console.log(`totalIbatBalance - ${totalIbatBalance}`);

    let input = document.getElementById("staking__amount_flexible");
    setNativeValue(input, maxAmount);
    input.dispatchEvent(new Event("input", { bubbles: true }));

    setStakeInputAmount(Moralis.Units.Token(maxAmount, IBAT_DECIMALS));
  };

  const setMaxUnstakeAmount = () => {
    let amount = userStakedIbat;
    console.log(`userStakedIbat - ${userStakedIbat}`);

    /**
     * @todo
     * maxAmountString - exists only to make sure that
     * when js rounds off the max amount to higher decimal than what was staked -
     * i just take off the last digit :'( until a better solution is found
     */
    let maxAmountString = amount.toString();
    console.log(`maxAmountString - ${maxAmountString}`);
    if (maxAmountString.length >= 17) {
      maxAmountString = maxAmountString.substring(
        0,
        maxAmountString.length - 1
      );
      amount = Number(maxAmountString);
      console.log(`substring- maxAmountString - ${maxAmountString}`);
    }

    // console.log(Moralis.Units.Token(amount, IBAT_DECIMALS));

    let input = document.getElementById("unstaking__amount");
    setNativeValue(input, amount);
    input.dispatchEvent(new Event("input", { bubbles: true }));

    setUnstakeInputAmount(Moralis.Units.Token(amount, IBAT_DECIMALS));
  };
  return (
    <>
      <div className="staking">
        <h2 className="staking__heading">Flexible Staking</h2>
        <div className="staking__apy">
          <span className="staking__tvl_text">APY:</span>
          <span
            className="staking__tvl_value"
            style={{ color: "#88D1F2", fontWeight: "700" }}
          >
            12%
          </span>
        </div>
        <div className="staking__tvl">
          <span className="staking__tvl_text">
            Total $IBAT in Flexible Staking
          </span>
          <span
            className="staking__tvl_value"
            style={{ color: "#88D1F2", fontWeight: "700" }}
          >
            {!firestoreLoading && firestoreValue && firestoreValue.exists()
              ? Number(firestoreValue.data().tvl).toLocaleString("en-US")
              : "Calculating..."}{" "}
            IBAT
          </span>
        </div>

        {isAuthenticated && (
          <>
            <section className="staking__container staking__selector gradient-2">
              <Tabs
                isFitted
                variant="enclosed"
                defaultIndex={0}
                style={{ color: "#C7D2ED" }}
              >
                <TabList style={{ borderBottom: "1px solid #4C5199" }}>
                  <Tab
                    w="50%"
                    style={{
                      borderTopRightRadius: "0px",
                      borderTopLeftRadius: "12px",
                      borderTop: "none",
                      borderLeft: "none",
                      borderBottom: "3px solid transparent",
                      textTransform: "uppercase",
                    }}
                    _selected={{
                      borderBottom: "5px solid #98a4f9 !important",
                      color: "#88D1F2",
                      fontWeight: "bold",
                      background:
                        "linear-gradient(90deg, rgba(76, 81, 153, 0.7) 0%, #161838 100%)",
                    }}
                  >
                    STAKE
                  </Tab>
                  <Tab
                    w="50%"
                    style={{
                      borderTopLeftRadius: "0px",
                      borderTopRightRadius: "12px",
                      borderTop: "none",
                      borderRight: "none",
                      borderBottom: "3px solid transparent",
                      textTransform: "uppercase",
                    }}
                    _selected={{
                      borderBottom: "5px solid #98a4f9 !important",
                      color: "#88D1F2",
                      fontWeight: "bold",
                      background:
                        "linear-gradient(270deg, rgba(76, 81, 153, 0.7) 0%, #161838 100%)",
                    }}
                  >
                    Unstake
                  </Tab>
                </TabList>
                <TabPanels>
                  <TabPanel className="staking__selector--amount">
                    <Stack
                      className="staking__amount_input"
                      margin="auto"
                      padding={"1.5rem"}
                    >
                      <label
                        className="staking__amount--label"
                        htmlFor="staking__amount"
                      >
                        Amount
                      </label>
                      <Text
                        gap=".5rem"
                        fontSize="xs"
                        className="staking__my_balance"
                      >
                        <span>~My balance: </span>
                        <span>{Number(totalIbatBalance).toFixed(3)} IBAT</span>
                      </Text>
                      <InputGroup>
                        <InputLeftAddon children="$IBAT" />
                        <NumberInput
                          id="staking__amount_flexible"
                          className="staking__amount"
                          min={0}
                          placeholder={"0 IBAT"}
                          precision={9}
                          clampValueOnBlur={false}
                          onChange={(e) => handleInputAmount(e, "stakeamount")}
                        >
                          <NumberInputField borderRadius={0} placeholder="0" />
                        </NumberInput>
                        <InputRightAddon
                          padding={0}
                          children={
                            <Button
                              bg="#161838"
                              className="staking__max_btn"
                              onClick={setMaxStakeAmount}
                            >
                              <Text color="white">Max</Text>
                            </Button>
                          }
                        />
                      </InputGroup>
                      <Text gap=".5rem" fontSize="xs" className="" align="left">
                        <span>
                          Min Stake Amount : &nbsp;
                          {Moralis.Units.FromWei(
                            MIN_DEPOSIT_AMOUNT,
                            IBAT_DECIMALS
                          )}{" "}
                          IBAT
                        </span>
                      </Text>
                      {flexAllowance === "true" ? (
                        <Button
                          className="btn btn--stake-ibat btn__connect_wallet btn-gradient-2"
                          onClick={() => stakeIbat()}
                          disabled={isFetching}
                        >
                          Stake IBAT
                        </Button>
                      ) : (
                        <Button
                          className="btn btn--stake-ibat btn__connect_wallet btn-gradient-2"
                          onClick={() =>
                            enableStaking(CONTRACT_ADDRESS_FLEXIBLE_STAKING)
                          }
                          disabled={isFetchingEnableStaking}
                        >
                          Enable Staking
                        </Button>
                      )}
                    </Stack>
                  </TabPanel>
                  <TabPanel className="staking__selector--amount">
                    <Stack
                      className="staking__amount_input"
                      margin="auto"
                      padding={"1.5rem"}
                    >
                      <label
                        className="staking__amount--label"
                        htmlFor="staking__amount"
                      >
                        Amount
                      </label>
                      <Text
                        gap=".5rem"
                        fontSize="xs"
                        className="staking__my_balance"
                      >
                        <span>~Staked IBAT: </span>
                        <span>{userStakedIbat}</span>
                      </Text>
                      <InputGroup>
                        <InputLeftAddon children="$IBAT" />
                        <NumberInput
                          id="unstaking__amount"
                          className="staking__amount"
                          clampValueOnBlur={false}
                          min={0}
                          precision={9}
                          onChange={(e) =>
                            handleInputAmount(e, "unstakeamount")
                          }
                        >
                          <NumberInputField borderRadius={0} placeholder="0" />
                        </NumberInput>
                        <InputRightAddon
                          padding={0}
                          children={
                            <Button
                              bg="#161838"
                              className="staking__max_btn"
                              onClick={setMaxUnstakeAmount}
                            >
                              <Text color="white">Max</Text>
                            </Button>
                          }
                        />
                      </InputGroup>
                      <Text gap=".5rem" fontSize="xs" className="" align="left">
                        <span>&nbsp;</span>
                      </Text>
                      <Button
                        className="btn btn--stake-ibat btn__connect_wallet btn-gradient-2"
                        onClick={() => unstakeIbat()}
                        disabled={isFetching}
                      >
                        Unstake IBAT
                      </Button>
                    </Stack>
                  </TabPanel>
                </TabPanels>
              </Tabs>
            </section>
            {window.localStorage.getItem("flexAllowance") === "true" &&
              isAuthenticated && (
                <section className="staking__container staking__rewards gradient-2">
                  <h3 className="staking__selector__heading">
                    My Rewards{" "}
                    <img
                      onClick={() => getUserStakedIbat()}
                      disabled={isFetching}
                      src={iconRefresh}
                      alt=""
                      style={{
                        width: "20px",
                        display: "inline",
                        position: "relative",
                        top: ".2rem",
                      }}
                    />
                  </h3>
                  <Grid
                    templateColumns="repeat(2, 1fr)"
                    columnGap="2"
                    px="1rem"
                    py="1rem"
                  >
                    <GridItem w="100%" minH="50px">
                      <Text fontSize="xs">Total Staked Amount</Text>
                      <Show below="md">
                        <Text fontWeight="700" fontSize="lg">
                          {Math.round(Number(userStakedIbat) * 1000) / 1000}
                          {" IBAT"}
                        </Text>
                      </Show>
                      <Show above="md">
                        <Text fontWeight="700" fontSize="2xl">
                          {Math.round(Number(userStakedIbat) * 1000) / 1000}
                          {" IBAT"}
                        </Text>
                      </Show>
                    </GridItem>
                    <GridItem w="100%" minH="50px">
                      <Text fontSize="xs">Total Rewards</Text>
                      <Show below="md">
                        <Text fontWeight="700" fontSize="lg">
                          {Math.round(
                            Number(
                              Moralis.Units.FromWei(userReward, IBAT_DECIMALS)
                            ) * 1000
                          ) / 1000}{" "}
                          {" IBAT"}
                        </Text>
                      </Show>
                      <Show above="md">
                        <Text fontWeight="700" fontSize="2xl">
                          {Math.round(
                            Number(
                              Moralis.Units.FromWei(userReward, IBAT_DECIMALS)
                            ) * 1000
                          ) / 1000}{" "}
                          {" IBAT"}
                        </Text>
                      </Show>
                    </GridItem>
                  </Grid>
                  <Flex direction="column">
                    <Button
                      className="btn btn--stake-ibat-rewards btn__connect_wallet btn-gradient-2"
                      onClick={() => stakeMyRewards()}
                      disabled={isFetching}
                    >
                      Stake my rewards
                    </Button>
                    <Button
                      className="btn--stake-ibat-rewards btn--stake-ibat-rewards-secondary"
                      bg="transparent"
                      textTransform="uppercase"
                      color="#C7D2ED"
                      textDecoration="underline"
                      _hover={{ backgroundColor: "#203464" }}
                      onClick={() => withdrawRewards()}
                      disabled={isFetching}
                    >
                      Withdraw Rewards
                    </Button>
                  </Flex>
                </section>
              )}
          </>
        )}
      </div>
    </>
  );
}

export default FlexibleStaking;
