import { useRadio, Box, Flex, Text } from "@chakra-ui/react";

function RadioCard(props) {
  const { getInputProps, getCheckboxProps } = useRadio(props);

  const input = getInputProps();
  const checkbox = getCheckboxProps();

  return (
    <Box as="label">
      <input {...input} />
      <Box
        {...checkbox}
        cursor="pointer"
        borderRadius="md"
        boxShadow="md"
        _checked={{
          background: "linear-gradient(98.76deg, #38C3CF 0%, #8767D3 100%)",
          color: "white",
          boxShadow: "1px outline",
        }}
        _hover={{
          background: "linear-gradient(98.76deg, #196067 0%, #502e9e 100%)",
          color: "white",
        }}
        _focus={{
          boxShadow: "1px outline",
          color: "white",
          background: "linear-gradient(98.76deg, #38C3CF 0%, #8767D3 100%)",
        }}
      >
        <Flex flexDirection="column">
          <Box
            px={2}
            py={1}
            borderTopRadius="md"
            borderWidth="1px"
            borderColor="#4C5199"
            fontSize="1rem"
            textColor="#C7D2ED"
            minWidth="60px"
          >
            <Text fontSize="min(3.25vw, 1rem)">
              {props.children.time}
              {` Days`}
            </Text>
          </Box>
          <Box
            px={2}
            py={1}
            borderBottomRadius="md"
            borderTop="none"
            borderWidth="1px"
            borderColor="#4C5199"
            fontSize=".85rem"
            bg="#181652"
            minWidth="60px"
          >
            <Text fontSize="min(3.15vw, .9rem)">
              {props.children.apy}
              {` APY`}
            </Text>
          </Box>
        </Flex>
      </Box>
    </Box>
  );
}

export default RadioCard;
